<template>
  <div>
    <h3> Settings </h3>
    <hr><br><br>
    <el-row :gutter="24">
      <el-col :span="6" v-if="!disableScanner">
        <b class="rfid">RFID SCANNER</b>
      </el-col>
      <el-col :span="6" v-if="!disableScanner">
        <el-switch
        v-model="value5"
        @change="getSwitch"
        active-color="#13ce66"
        inactive-color="#ff4949"
        active-text="ON"
        inactive-text="OFF">
        </el-switch>
      </el-col>
      <el-col :span="6">
        <b class="rfid">TRANSACTION</b>
      </el-col>
      <el-col :span="5">
        <el-switch
        v-model="transactionRedeemSwitch"
        @change="getTransactionRedeemSwitch"
        active-color="#13ce66"
        inactive-color="#ff4949"
        active-text="ON"
        inactive-text="OFF">
        </el-switch>
      </el-col>
    </el-row>
    <br><br><br>
    <el-row :gutter="24">
      <el-col :span="6">
        <b class="rfid">REDEEM ALL</b>
      </el-col>
      <el-col :span="6">
        <el-switch
        v-model="redeemAllSwitch"
        @change="getRedeemAllSwitch"
        active-color="#13ce66"
        inactive-color="#ff4949"
        active-text="ON"
        inactive-text="OFF">
        </el-switch>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      disableScanner: false,
      redeemAllSwitch: true,
      value5: true,
      transactionRedeemSwitch: true
    }
  },
  computed: {
    ...mapGetters('auth', {
      switch: 'switch'
    })
  },
  created () {
    this.getValue()
    this.getUser()
  },
  methods: {
    getUser () {
      const user = localStorage.getItem('users')
      this.user = JSON.parse(user)
      const role = this.user.roles.data[0].name
      console.log(role)
      if (role === 'sic') {
        this.disableScanner = true
      }
    },
    getSwitch () {
      localStorage.setItem('switch', JSON.stringify(this.value5))
      if (this.value5) {
        this.$notify({
          title: 'Success',
          message: 'RFID Scanner is ON!',
          type: 'success',
          duration: 2000
        })
      } else {
        this.$notify({
          title: 'Success',
          message: 'RFID Scanner is OFF!',
          type: 'error',
          duration: 2000
        })
      }
    },
    getTransactionRedeemSwitch () {
      localStorage.setItem('transactionRedeemSwitch', JSON.stringify(this.transactionRedeemSwitch))
      if (this.transactionRedeemSwitch) {
        this.$notify({
          title: 'Success',
          message: 'You can transact and redeem now!',
          type: 'success',
          duration: 2000
        })
      } else {
        this.$notify({
          title: 'Success',
          message: 'You cant transact and redeem!',
          type: 'error',
          duration: 2000
        })
      }
      window.location.reload()
    },
    getRedeemAllSwitch () {
      localStorage.setItem('redeemAll', JSON.stringify(this.redeemAllSwitch))
      if (this.redeemAllSwitch) {
        this.$notify({
          title: 'Success',
          message: 'You can now redeem all of your points!',
          type: 'success',
          duration: 2000
        })
      } else {
        this.$notify({
          title: 'Success',
          message: 'You cant redeem all your points!',
          type: 'error',
          duration: 2000
        })
      }
      window.location.reload()
    },
    getValue () {
      const toggle = localStorage.getItem('switch')
      this.value5 = JSON.parse(toggle)
      const transactRedeem = localStorage.getItem('transactionRedeemSwitch')
      this.transactionRedeemSwitch = JSON.parse(transactRedeem)
      const redeem = localStorage.getItem('redeemAll')
      this.redeemAllSwitch = JSON.parse(redeem)
    }
  }
}
</script>
<style scoped>
.rfid {
  font-size: 20px;
}
.switch {
  width: 100%;
  height: 100%;
}
</style>



