export default {
  testConnection (payload) {
    return axios.post(process.env.API_ENDPOINT + '/settings/database/connection', payload)
  },
  selectConnection (payload) {
    return axios.post(process.env.API_ENDPOINT + '/settings/database/set/connection', payload)
  }
}



