<template>
  <div>
    <el-row>
      <el-col :span="24">
          <p style="font-size: 20px"><i class="fa fa-pencil"></i> Transaction Update</p>
      </el-col>
    </el-row>
    <hr>
    <el-form label-width="120px" label-position="right" style="padding:20px" @submit.prevent="validateForm">
    <el-row :gutter="10">
        <el-col :span="12">
        <el-form-item label="Product">
            <el-select style="width: 100%" name="product" @change="populatePrice()" v-model="transaction.product_id" placeholder="Select" clearable v-validate= "'required'">
            <el-option
                v-for="item in filteredCustomerGroup"
                :key="item.id"
                :label="item.product.product_name"
                :value="item.product.id"
                >
            </el-option>
            </el-select>
            <span class="text-error">{{ errors.first('product') }}</span>
        </el-form-item>
        <el-form-item label="Amount">
            <el-input name="Amount" @change="selectLiters()" v-model="transaction.amount" v-validate="'required'" type="number">
            </el-input>
            <span class="text-error">{{ errors.first('Amount') }}</span>
        </el-form-item>
        </el-col>
        <el-col :span="12">
        <el-form-item label="Unit Price">
            <el-input name="Price" placeholder="0.00" v-validate="{required: true, decimal: 5}" readonly v-model="ruleForm.price" type="number" class="liter">
            <template slot="prepend">Php</template>
            <template slot="append">/Liter</template>
            </el-input>
            <span class="text-error">{{ errors.first('Price') }}</span>
        </el-form-item>
        <el-row :gutter="24">
            <el-col :span="12">
            <el-form-item label="Liters">
                <el-input name="Liters" v-model="ruleForm.liters" v-validate="'required'" readonly type="number"></el-input>
                <span class="text-error">{{ errors.first('Liters') }}</span>
            </el-form-item>
            </el-col>
            <el-col :span="12">
            <el-form-item label="Points/Liter">
                <el-input name="Liters" v-model="ruleForm.points" v-validate="'required'" readonly type="number"></el-input>
                <span class="text-error">{{ errors.first('Liters') }}</span>
            </el-form-item>
            </el-col>
        </el-row>
        </el-col>
    </el-row>
    <el-row :gutter="20">
        <el-form-item label="Remarks">
        <el-col :span="24">
        <el-input placeholder="Remarks" v-model="ruleForm.remarks" name="remarks" style="float:center"></el-input></el-col>
        </el-form-item>
    </el-row>
    <br>
    <el-form-item class="pull-right">
        <el-button type="submit" class="submit" @click="submitForm" :loading="isLoading" icon="fa fa-check-circle">&nbsp;Update</el-button>
        <el-button class="colorPrimary" icon="fa fa-times-circle" v-on:click="clear_form()">&nbsp;Clear</el-button>
    </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Transactions',
  data () {
    return {
      transact: true,
      notify: false,
      showGroup: false,
      selectedProduct: '',
      selectPoints: '',
      customerGroupPoints: [],
      customerCurrentTransaction: [],
      customerCurrentPoint: '',
      loading: false,
      isPoints: false,
      barcodeInfo: [],
      imageUrl: '',
      liter_round: '',
      presyo: '',
      isFiltered: false,
      isSuccess: true,
      customer_group_id: '',
      ruleForm: {
        id: '',
        name: '',
        group: '',
        discount: '',
        barcode: '',
        expiry_date: '',
        sales_date: Date.now(),
        type: '',
        avail: '',
        points: '',
        product_id: '',
        price: '',
        amount: '',
        liters: '',
        remarks: ''
      },
      productInfo: [],
      save: true
    }
  },
  computed: {
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('transaction', {
      filteredCustomer: 'transactionByBarcode',
      transaction: 'setTransaction'
    }),
    ...mapGetters('productManagement', {
      products: 'productsWithPoints'
    }),
    ...mapGetters('priceManagement', {
      prices: 'prices'
    }),
    ...mapGetters('auth', {
      shift: 'shift',
      user: 'currentUser'
    }),
    ...mapGetters('customerGroup', {
      filteredCustomerGroup: 'customerGroupPointSystem'
    }),
    ...mapGetters('pointsSystem', {
      points: 'points',
      pagination: 'pagination',
      response: 'response'
    })
  },
  created () {
    this.getCustomerGroup()
    this.getProducts()
    this.getPrices()
    this.getPoints()
    this.selectGroupPoints()
  },
  watch: {
    page () {
      this.getProducts()
      this.getPrices()
      this.getPoints()
    }
  },
  methods: {
    getProducts () {
      this.$store.dispatch('productManagement/getProducts', {page: null})
        .then(() => {
          this.populatePrice()
          this.selectLiters()
        })
    },
    getPrices () {
      this.$store.dispatch('priceManagement/getPrices')
    },
    getPoints () {
      this.$store.dispatch('pointsSystem/getPoints', { page: null })
    },
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup')
    },
    customerPoints () {
      this.$router.push('/reports_summary/customer_points')
      this.$store.dispatch('transaction/getTransactions', {customer_id: this.filteredCustomer[0].id, page: 1, from_date: 0, to_date: 0, dateOnly: 0})
    },
    submitForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          const form = {
            id: this.transaction.id,
            customer_id: this.transaction.customer.id,
            product_id: this.transaction.product_id,
            price: this.ruleForm.price,
            amount: this.transaction.amount,
            liters: this.ruleForm.liters,
            points: this.ruleForm.liters * this.ruleForm.points,
            customer_group_id: this.transaction.customer_group_id,
            shift: this.shift,
            remarks: this.ruleForm.remarks,
            username: this.user.username
          }
          if (this.transaction.amount <= 10000) {
            this.updateTransaction(form)
          } else {
            this.$notify({
              title: 'Error',
              message: 'Amount must not exceed within 10,000',
              type: 'error'
            })
          }
        }
      })
    },
    async updateTransaction (form) {
      this.$store.dispatch('transaction/subtractPoints', {
        customer_id: this.transaction.customer_id,
        points: this.transaction.points
      })
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('transaction/updateTransaction', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.customerCurrentTransaction = form
          this.transact = false
          this.notify = true
          // this.customerCurrentPoint = (this.customerCurrentTransaction.points).toFixed(2)
          // this.filteredCustomer[0].points = parseFloat(this.filteredCustomer[0].points) + parseFloat(this.customerCurrentPoint)
          this.clear_form()
          this.$router.push('/reports_summary/customer_points')
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Transaction limit has exceeded for this shift!',
            type: 'error'
          })
          throw new Error(error)
        })
    },
    clear_form: function () {
      this.ruleForm = {
        barcode: '',
        product_id: '',
        amount: '',
        price: '',
        remarks: ''
      }
    },
    validateForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.$notify({
            title: 'Success',
            message: 'Transaction Saved!',
            type: 'success',
            duration: 2000
          })
          this.$nextTick(() => {
            this.$validator.reset()
          })
          this.clear_form()
        }
      })
    },
    populatePrice () {
      this.selectedProduct = this.filteredCustomerGroup.filter(el => el.product_id === this.transaction.product_id)
      this.ruleForm.price = this.selectedProduct[0].product.price.product_price
      this.ruleForm.points = this.selectedProduct[0].equivalent_points
    },
    selectGroupPoints () {
      if (this.transaction.customer_group_id !== '') {
        this.$store.dispatch('customerGroup/getCustomerGroupById', {customer_group_id: this.transaction.customer_group_id})
      } else {
        this.filteredCustomerGroup = []
      }
    },
    selectLiters () {
      this.liter_round = this.transaction.amount / this.ruleForm.price
      this.ruleForm.liters = this.liter_round.toFixed(2)
    },
    goBack () {
      this.$router.push('/transactions')
      this.transact = true
      this.notify = false
    },
    goList () {
      this.$router.push('/reports_summary/customer_points')
    }
  }
}
</script>

<style scoped>
.text {
  font-size: 14px;
}

.item {
  padding: 18px 0;
}

.box-card {
  width: 100%;
}
.el-select {
  width: 100%;
}
.bg-purple-dark {
  background: #99a9bf;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.el-row {
  margin-bottom: 20px;
}
.el-row :last-child {
  margin-bottom: 0;
}
hr {
  margin-bottom: 20px;
}
.input-display {
  display: inline-block;
  margin-bottom: 0px;
}
.demo-input-size .el-input {
  vertical-align: top;
  margin: 0 10px 10px 0;
}
</style>



