<template>
  <div>
    <el-card v-show="hideBreadcrumb">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/customers/list' }">Customer List</el-breadcrumb-item>
      <el-breadcrumb-item>Customer Add</el-breadcrumb-item>
    </el-breadcrumb>
    </el-card>
    <br><br>
    <el-row>
      <el-col :span="24">
          <p style="font-size: 20px"><i class="fa fa-user-plus"></i> Customer Registration</p>
      </el-col>
    </el-row>
    <hr>
    <el-row :gutter="20">
      <el-form :model="ruleForm" label-position="left" ref="ruleForm" label-width="150px" class="demo-ruleForm">
        <el-col :span="12">
          <div style="margin-bottom: 22px;">
          <el-form-item label="Barcode">
            <el-input style="margin-bottom: 15px;" onkeyup="this.value = this.value.toUpperCase()" autofocus="true" name="Barcode" :class="errors.has('Barcode')? 'is-error':''" v-validate="'required'" v-model="ruleForm.barcode" placeholder="Barcode"></el-input>
            <span v-show="errors.has('Barcode')" class="text-error">This field is required</span>
          </el-form-item>
          </div>
          <el-form-item label="First Name">
            <el-input name="First Name" onkeyup="this.value=this.value.toUpperCase()" :class="errors.has('First Name')? 'is-error':''" v-validate="'required'" v-model="ruleForm.first_name" placeholder="Enter First Name"></el-input>
            <span v-show="errors.has('First Name')" class="text-error">{{ errors.first('First Name') }}</span>
          </el-form-item>
          <el-form-item label="Middle Initial" prop="middleInitial">
            <el-input v-model="ruleForm.middleInitial" onkeyup="this.value=this.value.toUpperCase()" placeholder="M.I" maxlength="1" style="width: 15%;"></el-input>
          </el-form-item>
          <el-form-item label="Last Name">
            <el-input name="Last Name" onkeyup="this.value = this.value.toUpperCase()" :class="errors.has('Last Name')? 'is-error':''" v-validate="'required'" v-model="ruleForm.last_name" placeholder="Enter Last Name"></el-input>
            <span v-show="errors.has('Last Name')" class="text-error">{{ errors.first('Last Name') }}</span>
          </el-form-item>
          <el-form-item label="Birthday">
            <el-date-picker name="Birthday" :class="errors.has('Birthday')? 'is-error':''" v-validate="'required'" type="date" placeholder="Pick a date" v-model="ruleForm.birthdate" style="width: 100%;"></el-date-picker>
            <span v-show="errors.has('Birthday')" class="text-error">{{ errors.first('Birthday') }}</span>
          </el-form-item>
          <el-form-item label="Address">
                <el-input name="city" onkeyup="this.value = this.value.toUpperCase()" :class="errors.has('city')? 'is-error':''" v-validate="'required'" title="CITY/MUNICIPALITY" placeholder="BRGY/CITY/PROVINCE" v-model="city"></el-input>
                <span v-show="errors.has('city')" class="text-error">This field is required</span>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="Contact Number">
            <el-input name="Contact Number" :class="errors.has('Contact Number')? 'is-error':''" v-validate="{ required: true, min:11, length:11, regex: /^([0-9]+)$/ }" v-model="ruleForm.mobile_no" maxlength="11"></el-input>
            <span v-show="errors.has('Contact Number')" class="text-error">{{ errors.first('Contact Number') }}</span>
          </el-form-item>
          <el-form-item label="Reference No.">
            <!-- v-validate="'required|email'" -->
            <el-input name="Reference No." :class="errors.has('Reference No.')? 'is-error':''" v-model="ruleForm.reference"></el-input>
            <span v-show="errors.has('Reference No.')" class="text-error">{{ errors.first('Reference No.') }}</span>
          </el-form-item>
          <el-form-item label="Vehicle">
            <el-select name="vehicle" multiple :class="errors.has('group')? 'is-error':''" v-validate="'required'" v-model="ruleForm.vehicle" placeholder="----- SELECT -----">
              <el-option
                v-for="item in vehicles"
                :key="item.id"
                :label="item.vehicle_name"
                :value="item.vehicle_name">
              </el-option>
            </el-select>
            <span v-show="errors.has('vehicle')" class="text-error">{{ errors.first('vehicle') }}</span>
          </el-form-item>
          <el-form-item label="Customer Group">
            <el-select name="group" :class="errors.has('group')? 'is-error':''" v-validate="'required'" @change="checkFleet" v-model="ruleForm.customerGroup" placeholder="----- SELECT -----">
              <el-option
                v-for="item in customerGroups"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <span v-show="errors.has('group')" class="text-error">{{ errors.first('group') }}</span>
          </el-form-item>
          <el-form-item label="Fleet Admin" v-show="showFleet">
            <el-row :gutter="20">
              <el-col :span="4">
                <el-switch v-model="ruleForm.is_admin" :disabled="disableAdmin"></el-switch>
              </el-col>
              <el-col :span="16">
                <el-alert v-show="disableAdmin" :title="adminName" :closable="false" type="warning"></el-alert>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item>
            <el-button class="submit" round @click="submitForm" :loading="isLoading" :disabled="isDisable"><i class="fa fa-check"></i> Submit</el-button>
            <el-button class="colorPrimary" round @click="resetForm('ruleForm')"><i class="fa fa-undo"></i> Reset</el-button>
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'
import moment from 'moment'

export default {
  data () {
    return {
      adminName: '',
      disableAdmin: false,
      showFleet: false,
      shift: '',
      user: '',
      hideBreadcrumb: false,
      expire_at: moment().add(1, 'years').format('YYYY-MM-DD'),
      city: '',
      barcode: '',
      ruleForm: {
        is_admin: false,
        barcode: '',
        first_name: '',
        middleInitial: '',
        last_name: '',
        birthdate: '',
        address: '',
        mobile_no: '',
        reference: '',
        customerGroup: '',
        vehicle: ''
      }
    }
  },
  computed: {
    ...mapGetters('customer', {
      customers: 'customers'
    }),
    ...mapGetters('fleetCard', {
      fleets: 'fleets'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('customerGroup', {
      customerGroups: 'customerGroups'
    }),
    ...mapGetters('vehicleManagement', {
      vehicles: 'vehicles'
    })
  },
  created () {
    this.getCredentials()
    this.getUser()
    this.getCustomerGroup()
    this.getVehicles()
    this.userPermission()
    this.getCustomers()
    this.getFleetCard()
  },
  methods: {
    checkFleet  () {
      const fleet = this.fleets.filter(el => el.customer_group_id === this.ruleForm.customerGroup)
      const customer = this.customers.filter(el => el.customer_group_id === this.ruleForm.customerGroup)
      const admin = customer.filter(el => el.is_admin === 1)
      if (fleet.length > 0) {
        this.showFleet = true
        if (customer.length > 0) {
          if (admin.length > 0) {
            this.ruleForm.is_admin = false
            this.disableAdmin = true
            this.adminName = admin[0].first_name + ' ' + admin[0].last_name + ' is admin!'
          } else {
            this.ruleForm.is_admin = false
            this.disableAdmin = false
          }
        } else {
          this.ruleForm.is_admin = false
          this.disableAdmin = false
        }
      } else {
        this.showFleet = false
      }
    },
    getCredentials () {
      const credentials = localStorage.getItem('credentials')
      const credential = JSON.parse(credentials)
      this.shift = credential.shift
    },
    getUser () {
      const user = localStorage.getItem('users')
      this.user = JSON.parse(user)
    },
    userPermission () {
      const userRole = this.user.roles.data[0].name
      if (userRole === 'admin') {
        this.hideBreadcrumb = true
      } else {
        this.hideBreadcrumb = false
      }
    },
    async getCustomers () {
      this.$store.dispatch('customer/getCustomers', { page: null })
    },
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup', {page: null})
    },
    getFleetCard () {
      this.$store.dispatch('fleetCard/getFleetCard', {page: null})
    },
    getVehicles () {
      this.$store.dispatch('vehicleManagement/getVehicles', {page: null})
    },
    submitForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          const form = {
            barcode: 'mg' + '-' + this.ruleForm.barcode,
            first_name: this.ruleForm.first_name.toUpperCase(),
            last_name: this.ruleForm.last_name.toUpperCase(),
            birthdate: moment(this.ruleForm.birthdate).format('YYYY-MM-DD'),
            address: this.city.toUpperCase(),
            mobile_no: this.ruleForm.mobile_no,
            customer_group_id: this.ruleForm.customerGroup,
            expire_at: this.expire_at,
            vehicles: (this.ruleForm.vehicle).join(', '),
            middle_initial: this.ruleForm.middleInitial.toUpperCase(),
            shift: this.shift,
            username: this.user.username,
            reference_no: this.ruleForm.reference.toUpperCase(),
            is_admin: this.ruleForm.is_admin ? 1 : 0
          }
          this.createCustomer(form)
        }
      })
    },
    async createCustomer (form) {
      const customer = this.customers.find(el => el.reference_no === this.ruleForm.reference)
      if (customer === undefined) {
        this.$store.commit('appUI/setIsLoading', true)
        this.$store.commit('appUI/setIsDisable', true)
        this.$store.dispatch('customer/addCustomer', form)
          .then(() => {
            this.$store.commit('appUI/setIsLoading', false)
            this.$store.commit('appUI/setIsDisable', false)
            this.$notify({
              title: 'Success',
              message: 'Customer Added Successfully!',
              type: 'success'
            })
            this.getCustomers()
            this.getFleetCard()
            this.resetForm()
          })
          .catch(error => {
            this.$store.commit('appUI/setIsLoading', false)
            this.$store.commit('appUI/setIsDisable', false)
            this.$notify({
              title: 'Error',
              message: 'The barcode has already been taken.',
              type: 'error'
            })
            throw new Error(error)
          })
      } else {
        this.$notify({
          title: 'Error',
          message: 'Serial Number is Already Taken!',
          type: 'error'
        })
      }
    },
    resetForm () {
      this.city = ''
      this.barcode = ''
      this.showFleet = false
      this.ruleForm = {
        is_admin: false,
        barcode: '',
        first_name: '',
        middleInitial: '',
        last_name: '',
        birthdate: '',
        address: '',
        mobile_no: '',
        email: '',
        customerGroup: '',
        vehicle: [],
        reference: ''
      }
      this.$nextTick(() => this.$validator.reset())
    }
  }
}
</script>

<style scoped>
.text {
  font-size: 14px;
}
.item {
  padding: 18px 0;
}
.box-card {
  width: 100%;
}
.el-select {
  width: 100%;
}
.bg-purple-dark {
  background: #99a9bf;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.el-row {
  margin-bottom: 20px;
}
.el-row :last-child {
  margin-bottom: 0;
}
hr {
  margin-bottom: 20px;
}
.input-display {
  display: inline-block;
  margin-bottom: 0px;
}
.demo-input-size .el-input {
  vertical-align: top;
  margin: 0 10px 10px 0;
}
</style>



