<template>
    <div>
      <h2 style="text-align: center">EXPORT TO EXCEL OR DATABASE</h2>
      <hr>
      <br><br><br>
      <el-row v-if="showMonth" :gutter="24">
        <el-col :span="12">
          <el-form label-width="130px">
            <el-form-item label="Select Month: ">
              <el-date-picker
                style="width: 70%"
                value-format="MM"
                v-model="selectMonth"
                type="month"
                format="MMMM"
                placeholder="Pick a month">
              </el-date-picker>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12">
          <el-form label-width="130px">
            <el-form-item label="Select Year: ">
              <el-date-picker
                style="width: 70%"
                v-model="selectYear"
                type="year"
                placeholder="Pick a Year">
              </el-date-picker>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <el-row v-if="showGroup" :gutter="24">
        <el-col :span="12">
          <el-form label-width="130px">
            <el-form-item label="Customer Group">
              <el-select name="group" :class="errors.has('group')? 'is-error':''" v-validate="'required'" v-model="customerGroup" placeholder="----- SELECT -----">
                <el-option
                  v-for="item in customerGroups"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
              <span v-show="errors.has('group')" class="text-error">{{ errors.first('group') }}</span>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <br><br>
      <el-row :gutter="24">
        <el-col :span="14">
          <el-form>
            <el-select class="input select" @change="selectbranch" style="width: 88%;margin-left: 37px;" clearable v-model="selectExport" placeholder="----- SELECT -----">
              <el-option label="Backup(Sql File).sql" value="database"></el-option>
              <el-option label="Price Update Summaries" v-if="sicPermission" value="priceExport"></el-option>
              <el-option label="Points Update Summaries" v-if="sicPermission" value="pointsExport"></el-option>
              <el-option label="Customer Registration Logs" v-if="sicPermission" value="customerExport"></el-option>
              <el-option label="Transaction Logs" value="transactionExport"></el-option>
              <el-option label="Redeem Logs" value="redeemExport"></el-option>
              <el-option label="Points Conversion" v-if="sicPermission" value="pointConversion"></el-option>
              <el-option label="Tagged Customers" v-if="sicPermission" value="taggedCustomers"></el-option>
              <el-option label="Promo Availed by Customers" v-if="sicPermission" value="promoAvailed"></el-option>
              <el-option label="Top Customers" v-if="sicPermission" value="topCustomers"></el-option>
              <el-option label="General Template" value="generalTemplate"></el-option>
              <el-option label="Card Volume" value="exportCardVolume"></el-option>
              <el-option label="Total Earned Points" value="exportEarnedPoints"></el-option>
              <el-option label="Total Redeemed Points" value="exportTotalRedeem"></el-option>
              <el-option label="Total UnRedeemed Points" value="exportUnredeemed"></el-option>
            </el-select>
          </el-form>
        </el-col>
        <el-col :span="8">
          <a v-on:click='exportNow' style="cursor: pointer">
            <el-card shadow="hover" id="tile" class="tile-yellow">
              <h1 style="text-align: center;"><i class="fa fa-download"></i>&nbsp;&nbsp;<b>EXPORT NOW</b></h1>
            </el-card>
          </a>
        </el-col>
      </el-row>
      <br><br><br>
    </div>
</template>
<script>
import moment from 'moment'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      selectedMonthExport: [
        { name: 'generalTemplate' },
        { name: 'pointConversion' },
        { name: 'exportCardVolume' },
        { name: 'exportEarnedPoints' },
        { name: 'exportTotalRedeem' }
      ],
      sicPermission: true,
      showGroup: false,
      customerGroup: '',
      selectMonth: '',
      selectYear: '',
      selectedBranch: '',
      showMonth: false,
      url: '',
      selectExport: ''
    }
  },
  computed: {
    ...mapGetters('databaseManagement', {
      exportDatabase: 'exportDatabase',
      response: 'response'
    }),
    ...mapGetters('customerGroup', {
      customerGroups: 'customerGroups'
    })
  },
  watch: {
    page () {
      this.getDatabases()
    }
  },
  created () {
    this.getConnectionsFromLocalStorage()
    this.getDatabases()
    this.getCustomerGroup()
    this.getUser()
  },
  methods: {
    getUser () {
      const user = localStorage.getItem('users')
      this.user = JSON.parse(user)
      const userRole = this.user.roles.data[0].name
      if (userRole === 'sic') {
        this.sicPermission = false
      }
    },
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup', {page: null})
    },
    month () {
      const month = moment().format('MM')
      this.selectMonth = month
      const year = moment().format('YYYY')
      this.selectYear = year
    },
    exportNow () {
      switch (this.selectExport) {
        case 'database':
          this.handleDownload()
          break
        case 'priceExport':
          this.exportPrice()
          break
        case 'pointsExport':
          this.exportPoints()
          break
        case 'customerExport':
          this.exportCustomer()
          break
        case 'transactionExport':
          this.exportTransaction()
          break
        case 'redeemExport':
          this.exportRedeem()
          break
        case 'generalTemplate':
          this.exportGeneral()
          break
        case 'pointConversion':
          this.exportRedeemByMonth()
          break
        case 'taggedCustomers':
          this.exportTagCustomers()
          break
        case 'promoAvailed':
          this.exportPromoAvailed()
          break
        case 'exportCardVolume':
          this.exportCardVolume()
          break
        case 'exportEarnedPoints':
          this.exportEarnedPoints()
          break
        case 'exportTotalRedeem':
          this.exportTotalRedeem()
          break
        case 'exportUnredeemed':
          this.exportUnredeemed()
          break
        case 'topCustomers':
          this.exportTopCustomer()
          break
      }
    },
    selectbranch () {
      const exported = this.selectedMonthExport.filter(el => el.name === this.selectExport)
      if (exported.length > 0) {
        this.showMonth = true
        this.month()
        this.getConnectionsFromLocalStorage()
      } else if (this.selectExport === 'topCustomers') {
        this.showGroup = true
      } else {
        this.showMonth = false
        this.showGroup = false
      }
    },
    getConnectionsFromLocalStorage () {
      const connections = localStorage.getItem('connections')
      this.connections = connections ? JSON.parse(connections) : []
      this.connectiondatabase = localStorage.getItem('connectiondatabase')
      this.branch = this.connections.filter(el => el.isSelected === true)
      this.selectedBranch = this.branch[0].branch_name
    },
    getDatabases () {
      this.$store.dispatch('databaseManagement/getDatabases', {page: null})
    },
    handleDownload () {
      this.url = this.exportDatabase[0].path
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Database Successfully Exported!',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportCardVolume () {
      this.url = process.env.API_ENDPOINT + '/card-volume?export=1&month=' + this.selectMonth + '&year=' + this.selectYear
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Card Volume Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.showMonth = false
      this.clearForm()
    },
    exportEarnedPoints () {
      this.url = process.env.API_ENDPOINT + '/earn-point?export=1&month=' + this.selectMonth + '&year=' + this.selectYear
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Total Earned Points Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.showMonth = false
      this.clearForm()
    },
    exportTotalRedeem () {
      this.url = process.env.API_ENDPOINT + '/total-redeem?export=1&month=' + this.selectMonth + '&year=' + this.selectYear
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Total Redeemed Points Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.showMonth = false
      this.clearForm()
    },
    exportUnredeemed () {
      this.url = process.env.API_ENDPOINT + '/unredeem?export=1&month=' + this.selectMonth + '&year=' + this.selectYear
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Total UnRedeemed Points Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.showMonth = false
      this.clearForm()
    },
    exportGeneral () {
      this.url = process.env.API_ENDPOINT + '/export-daily?export=1&month=' + this.selectMonth + '&year=' + this.selectYear + '&station=' + this.selectedBranch
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'General Template Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.showMonth = false
      this.clearForm()
    },
    exportPrice () {
      this.url = process.env.API_ENDPOINT + '/export-price-summary'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Price Update Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportPoints () {
      this.url = process.env.API_ENDPOINT + '/export-point-system-summary'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Points Update Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportCustomer () {
      this.url = process.env.API_ENDPOINT + '/customer-logs'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Customer Logs Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportTransaction () {
      this.url = process.env.API_ENDPOINT + '/transaction-logs'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Transaction Logs Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportRedeem () {
      this.url = process.env.API_ENDPOINT + '/redeem-logs'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Redeem Logs Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportRedeemByMonth () {
      this.url = process.env.API_ENDPOINT + '/points-redeem?filter_by_customer=0&month=' + this.selectMonth + '&year=' + this.selectYear
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Point Conversion Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.showMonth = false
      this.clearForm()
    },
    exportTagCustomers () {
      this.url = process.env.API_ENDPOINT + '/promo-customer-avail?export=1'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Tagged Customer Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportPromoAvailed () {
      this.url = process.env.API_ENDPOINT + '/promo-customer-avail?export_availed=1'
      window.location.href = this.url
      this.$notify({
        title: 'Success',
        message: 'Promo Availed by Customer Successfully Exported',
        type: 'success',
        duration: 2000
      })
      this.clearForm()
    },
    exportTopCustomer () {
      if (this.customerGroup !== '') {
        this.url = process.env.API_ENDPOINT + '/customer-high?export=1&type=current&points=0&filter_customer_group_id=' + this.customerGroup
        window.location.href = this.url
        this.$notify({
          title: 'Success',
          message: 'Top Customer by Customer Group Successfully Exported',
          type: 'success',
          duration: 2000
        })
        this.clearForm()
        this.showGroup = false
        this.customerGroup = ''
      } else {
        this.$notify({
          title: 'Error',
          message: 'Please select customer group',
          type: 'error',
          duration: 2000
        })
      }
    },
    clearForm () {
      this.selectExport = ''
    }
  }
}
</script>



