import api from '../api/promoItems'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    promos: [],
    pagination: '',
    response: []
  },
  getters: {
    promos: state => state.promos,
    pagination: state => state.pagination,
    response: state => state.response
  },
  mutations: {
    [types.mutations.promoItems.setPromo] (state, payload) {
      state.promos = payload
    },
    [types.mutations.promoItems.setResponse] (state, payload) {
      state.response = payload
    }
  },
  actions: {
    async [types.actions.promoItems.getPromos] ({ commit }, payload) {
      try {
        const response = await api.getPromos(payload)
        commit(types.mutations.promoItems.setPromo, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.promoItems.addPromo] ({ commit }, payload) {
      try {
        const response = await api.addPromo(payload)
        commit(types.mutations.promoItems.setResponse, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.promoItems.updatePromo] (state, payload) {
      try {
        await api.updatePromo(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.promoItems.deletePromo] (state, payload) {
      try {
        await api.deletePromo(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}



