import api from '../api/fleetCard'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    pagination: '',
    fleets: []
  },
  getters: {
    pagination: state => state.pagination,
    fleets: state => state.fleets
  },
  mutations: {
    [types.mutations.fleetCard.setFleetCard] (state, payload) {
      if (payload.data === undefined) {
        state.fleets = payload
        state.pagination = payload
      } else {
        state.fleets = payload.data
        state.pagination = payload
      }
    },
    [types.mutations.fleetCard.setFleetCard] (state, payload) {
      state.fleets = payload
    }
  },
  actions: {
    async [types.actions.fleetCard.getFleetCard] ({ commit }, payload) {
      try {
        const response = await api.getFleetCard(payload)
        commit(types.mutations.fleetCard.setFleetCard, response.data)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}



