export default {
  getPromoCustomer (payload = {}) {
    return axios.get(process.env.API_ENDPOINT + '/promo-customer-avail?customer_id=' + payload.customer_id)
  },
  getTagCustomers () {
    return axios.get(process.env.API_ENDPOINT + '/tag-customer')
  },
  addPromoCustomer (payload = {}) {
    return axios.post(process.env.API_ENDPOINT + '/promo-customer-avail', payload)
  },
  updatePromoCustomer (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.API_ENDPOINT + `/promo-customer-avail/${payload.id}`, payload)
  }
}



