export default {
  getGenerateTemplate (payload = {}) {
    const page = payload.page !== null ? `?page=${payload.page}` : ''
    return axios.get(process.env.API_ENDPOINT + '/export-daily' + page)
  },
  addGenerateTemplate (payload = {}) {
    return axios.post(process.env.API_ENDPOINT + '/export-daily', payload)
  },
  updateGenerateTemplate (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.API_ENDPOINT + `/export-daily/${payload.id}`, payload)
  },
  deleteGenerateTemplate (payload = {}) {
    return axios.delete(process.env.API_ENDPOINT + `/export-daily/${payload.id}`, payload)
  }
}



