<template>
    <el-dialog :before-close="handleClose" :title="title" :visible.sync="dialogVisible">
      <slot name="body"></slot>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitTagPromo" :loading="loading" :disabled="disabled" class="colorSuccess"><i class="fa fa-plus"></i> Tag Customer</el-button>
        <el-button @click="onCancel" class="colorPrimary"><i class="fa fa-times-circle"></i> Close</el-button>
      </div>
  </el-dialog>
</template>

<script>
export default {
  props: {
    dialogVisible: '',
    title: '',
    footer: '',
    loading: '',
    showButtonSave: ''
  },
  data () {
    return {
      user: '',
      disabled: false
    }
  },
  created () {
    this.getUser()
  },
  methods: {
    getUser () {
      const users = localStorage.getItem('users')
      this.user = JSON.parse(users)
      const user = this.user.roles.data[0].name
      if (user === 'admin') {
        this.disabled = false
      } else {
        this.disabled = true
      }
    },
    handleClose () {
      this.$emit('handleClose')
    },
    onSubmit () {
      this.$emit('onSubmit')
    },
    submitTagPromo () {
      this.$emit('submitTagPromo')
    },
    onCancel () {
      this.$emit('onCancel')
    }
  }
}
</script>



