<template>
    <div>
      <app-modal
      :dialogVisible="redeemDialog"
      :loading="isLoading"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit"
      >
      <div slot="body">
        <el-form status-icon label-width="120px" >
          <b>Please Enter Customer's Birthday to Verify.</b>
          <br><br>
            <span style="font-size: 15px; font-family: sans-serif">BIRTHDAY:</span>
            <el-date-picker
              v-model="birthday"
              style="margin-left: 27px;width: 400px"
              type="date"
              placeholder="Pick a day"
              value-format="yyyy-MM-dd">
            </el-date-picker>
          <br>
        </el-form>
      </div>
    </app-modal>
        <el-steps :active="active" finish-status="success">
            <el-step title="Step 1"></el-step>
            <el-step title="Processing"></el-step>
            <el-step title="Finish"></el-step>
        </el-steps>
        <br><br><br><br>
        <div v-if="active === 1">
          <center>
              <el-row :gutter="20">
                <el-col :span="20" :offset="2">
                  <h1 v-if="condition">Please Tap your Card to Redeem!</h1>
                  <el-input v-if="inputField" name="code" @input="onInputBarcode" v-model="barcode" autofocus class="input code" placeholder="Please Tap Your Card To Redeem..."></el-input>
                </el-col>
              </el-row>
          </center>
        </div>
        <div v-else-if="active === 2">
              <el-form label-width="150px">
                <el-col :span="12">
                  <el-form-item style="font-weight: bold" label="CUSTOMER NAME:" :inline="true">
                    <el-input readonly v-model="fullName"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item style="font-weight: bold" label="BARCODE:" :inline="true">
                    <el-input readonly v-model="barCode"></el-input>
                  </el-form-item>
                </el-col>
              </el-form>
            <br><br><br><br>
            <el-row>
            <el-col :span="10">
              <div style="text-align: center">
                <p class="current">CURRENT POINTS</p><br>
                <b class="points">{{ adminPoints ? this.filteredCustomer[0].points : fleetPoints}}</b><br><br><br><br><br>
              <el-row :gutter="24">
                <el-col :span="12" :offset="6">
                  <el-button type="primary" @click="redeemAll" v-if="redeemAllSwitch" :disabled="redeemDisable" style="width: 100%">REDEEM ALL</el-button>
                </el-col>
              </el-row>
              </div>
            </el-col>
            <el-col :span="14">
                <el-card>
                    <el-form status-icon :label-position="labelPosition" label-width="120px" >
                        <el-form-item label="Type">
                          <el-input name="type" style="width: 100%" :disabled="disableRedeem" :class="errors.has('type')? 'is-error':''" v-validate="'required'" v-model="redeem_type" placeholder="Select Redeem Type"></el-input>
                          <span v-show="errors.has('type')" class="text-error">{{ errors.first('type') }}</span>
                        </el-form-item>
                        <el-form-item label="Serial Number" prop="checkPass">
                            <el-input name="Serial Number" style="width: 100%" v-model="serial_number" :class="errors.has('Serial Number')? 'is-error':''" v-validate="'required'" placeholder=" Enter Serial Number" auto-complete="off"></el-input>
                            <span v-show="errors.has('Serial Number')" class="text-error">{{ errors.first('Serial Number') }}</span>
                        </el-form-item>
                        <el-form-item label="Amount">
                          <el-select name="amount" style="width: 100%" :disabled="disableAmount" clearable :class="errors.has('amount')? 'is-error':''" v-validate="'required'" v-model="amount_redeem" placeholder="Select Amount">
                            <el-option label="60" value="60.00"></el-option>
                            <el-option label="100" value="100.00"></el-option>
                            <el-option label="200" value="200.00"></el-option>
                            <el-option label="500" value="500.00"></el-option>
                            <el-option label="1000" value="1000.00"></el-option>
                          </el-select>
                          <span v-show="errors.has('amount')" class="text-error">{{ errors.first('amount') }}</span>
                        </el-form-item>
                        <el-form-item>
                          <el-button type="success" @click="submitForm" :disabled="fleetDisable" style="width: 100%">REDEEM</el-button>
                        </el-form-item>
                        <el-form-item>
                          <el-alert
                            v-if="fleetDisable"
                            title="You're not allowed to redeem!"
                            type="warning">
                          </el-alert>
                        </el-form-item>
                    </el-form>
                </el-card>
            </el-col>
        </el-row>
        <!-- <br><br>
        <el-row>
            <el-card class="box-card" shadow="never">
              <template>
                <div slot="header" class="clearfix">
                  <span id="span_header_2"><b>LIST OF PROMOS YOU CAN AVAIL</b></span>
                </div>
                  <el-table
                  :data="promos"
                  border>
                  <el-table-column
                    label="NAME"
                    prop="name"
                    fixed>
                  </el-table-column>
                  <el-table-column
                    prop="eq_points"
                    label="EQUIVALENT POINTS">
                  </el-table-column>
                  <el-table-column
                    prop="date_start"
                    label="DATE START">
                  </el-table-column>
                  <el-table-column
                    prop="date_end"
                    label="DATE END">
                  </el-table-column>
                  <el-table-column
                    prop="liters"
                    label="ACTION">
                    <template slot-scope="scope">
                      <el-button
                        type="success"
                        class="button"
                        @click="handleRedeem(scope.row)"
                        size="mini">
                        REDEEM PROMO
                      </el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </template>
            </el-card>
          </el-row> -->
        </div>
        <div v-else-if="active === 3">
          <el-col :span="10" :offset="7">
            <el-card>
              <div slot="header">
                <br>
                <i class="fa fa-check-circle-o" style="margin-left: 39%;font-size: 80px;color: #51b156" aria-hidden="true"></i>
                <p style="margin-left: 35%;font-size: 21px;color: #51b156">SUCCESS</p>
              </div>
              <div class="text item" style="margin-left: 8%;font-size: 17px;color: gray">
                <br>
                You have successfully redeem your points!
                <br><br>
                <center>
                  <b class="note">Note: You only have {{ availablePoints }} Points.</b>
                </center>
                <br><br>
                <el-button style="margin-left: 33%" type="success" @click="goBack" round>Done</el-button>
              </div>
            </el-card>
            <br><br><br>
          </el-col>
        </div>
        <br><br><br><br>
    </div>
</template>
<script>
// import moment from 'moment'
import RedeemModal from '@/components/RedeemModal'
import { mapGetters } from 'vuex'
import _ from 'lodash'
export default {
  components: {
    'app-modal': RedeemModal
  },
  data () {
    return {
      fleet_tin: '',
      adminPoints: true,
      fleetDisable: '',
      customer_redeem: '',
      fleetPoints: 0,
      scanner: true,
      redeemAllSwitch: true,
      redeemDisable: false,
      user: '',
      shift: '',
      switch: '',
      inputField: false,
      default: 0,
      condition: '',
      disableAmount: false,
      disableRedeem: false,
      redeem_type: '',
      availablePoints: '',
      currentPoints: '',
      labelPosition: 'left',
      serial_number: '',
      birthday: '',
      loading: false,
      redeemDialog: false,
      amount_redeem: '',
      active: 1,
      barcode: '',
      isFiltered: false,
      isSuccess: true
    }
  },
  created () {
    this.getTagCustomers()
    this.getCustomers()
    this.$barcodeScanner.init(this.onBarcodeScanned)
    this.getUsernameShift()
    this.getFleetCard()
    // this.redeemNotif()
    this.getPointsRedeem()
    this.getSwitch()
  },
  computed: {
    ...mapGetters('transaction', {
      filteredCustomer: 'transactionByBarcode'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading'
    }),
    ...mapGetters('customer', {
      customers: 'customers'
    }),
    ...mapGetters('pointsRedeem', {
      redeem: 'redeem',
      redeemPoints: 'redeemPoints'
    }),
    ...mapGetters('fleetCard', {
      fleets: 'fleets'
    }),
    ...mapGetters('promoCustomerAvail', {
      tagCustomers: 'tagCustomers'
    }),
    ...mapGetters('promoItems', {
      promos: 'promos'
    }),
    fullName: function () {
      return this.filteredCustomer[0] ? this.filteredCustomer[0].first_name + ' ' + this.filteredCustomer[0].last_name : ''
    },
    barCode: function () {
      return this.filteredCustomer[0] ? this.filteredCustomer[0].barcode : ''
    },
    pointsCustomer () {
      return this.filteredCustomer[0].points
    }
  },
  methods: {
    getTagCustomers () {
      this.$store.dispatch('promoCustomerAvail/getTagCustomers')
    },
    getCustomers () {
      this.$store.dispatch('customer/getCustomers', {page: null})
    },
    getFleetCard () {
      this.$store.dispatch('fleetCard/getFleetCard', {page: null})
    },
    checkFleet () {
      const fleet = this.fleets.filter(el => el.customer_group_id === this.filteredCustomer[0].customer_group_id)
      if (fleet.length > 0) {
        this.fleetDisable = false
        if (this.filteredCustomer[0].is_admin) {
          this.fleet_tin = fleet.tin_no
          this.adminPoints = false
          this.disableAmount = true
          const customer = this.customers.filter(el => el.customer_group_id === this.filteredCustomer[0].customer_group_id)
          this.customer_redeem = customer.map(el => el.id)
          const amountArr = customer.map(el => parseFloat(el.points))
          const reducer = (accumulator, currentValue) => accumulator + currentValue
          const finalPoints = amountArr.reduce(reducer).toFixed(2)
          this.fleetPoints = finalPoints.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
          this.amount_redeem = this.fleetPoints
        } else {
          this.fleetDisable = true
          console.log('failed')
        }
      } else {
        this.fleetDisable = false
      }
      this.redeemDisableFunction()
    },
    getRedeemPrint (form) {
      const promo = this.tagCustomers.filter(el => el.customer_id === this.filteredCustomer[0].id)
      const availablePromo = promo.filter(el => el.isAvail === 'false')
      const user = localStorage.getItem('credentials')
      const station = localStorage.getItem('connections')
      const stations = JSON.parse(station)
      const username = JSON.parse(user)
      const barcode = this.filteredCustomer[0].barcode
      const cover = barcode.toString().slice(-3)
      const data = {
        name: this.filteredCustomer[0].first_name + ' ' + this.filteredCustomer[0].last_name,
        barcode: 'my-*******' + cover,
        total_points: this.filteredCustomer[0].points,
        redeem: this.amount_redeem,
        remaining_points: this.availablePoints,
        promo: availablePromo.length ? promo[0].promo_item.name : 'NONE',
        tax: form.tax,
        transact_by: username.username,
        tin: this.fleet_tin,
        station: stations[0].branch_name
      }
      this.$store.dispatch('pointsRedeem/getRedeemPrint', data)
    },
    redeemDisableFunction () {
      if (this.filteredCustomer[0].points > 0 || this.fleetPoints > 0) {
        this.redeemDisable = false
      } else {
        this.redeemDisable = true
      }
    },
    redeemAll () {
      this.amount_redeem = this.adminPoints ? this.filteredCustomer[0].points : this.fleetPoints
      this.disableAmount = !this.adminPoints
    },
    getUsernameShift () {
      const user = localStorage.getItem('users')
      this.user = JSON.parse(user)
      const shift = localStorage.getItem('credentials')
      this.shift = JSON.parse(shift)
      const redeem = localStorage.getItem('redeemAll')
      this.redeemAllSwitch = JSON.parse(redeem)
    },
    getSwitch () {
      const toggle = localStorage.getItem('switch')
      this.switch = JSON.parse(toggle)
      this.condition = this.switch
      if (this.switch === false) {
        this.inputField = true
      } else {
        this.inputField = false
      }
    },
    getPointsRedeem () {
      this.$store.dispatch('pointsRedeem/getPointsRedeem', {
        customer_id: 0,
        page: null,
        from_date: 0,
        to_date: 0,
        dateOnly: 0,
        shift: 0,
        export: 0
      })
    },
    getPromos () {
      this.points = this.filteredCustomer[0].points
      this.$store.dispatch('promoItems/getPromos', {points: this.points})
    },
    onInputBarcode: _.debounce(function (event) {
      // this.barcode = event
      this.scanner = false
      if (this.barcode !== '') {
        this.isFiltered = true
        this.loading = true
        this.$store.commit('appUI/setIsLoading', true)
        this.$store.dispatch('transaction/getCustomerByBarcode', {barcode: 'mg-' + this.barcode})
          .then(() => {
            this.$store.commit('appUI/setIsLoading', false)
            if (this.filteredCustomer.length === 1) {
              this.loading = false
              this.isSuccess = false
              this.redeemDialog = true
              // this.getPromos()
            } else {
              this.isSuccess = true
              this.$notify({
                title: 'Error',
                message: 'Barcode doesnt exists!',
                type: 'error',
                duration: 1000
              })
            }
          })
          .catch(error => {
            this.$notify({
              title: 'Error',
              message: 'Somethig went wrong!',
              type: 'error',
              duration: 1000
            })
            throw new Error(error)
          })
      } else {
        this.isFiltered = false
      }
      this.isDisable()
    }, 500),

    isDisable () {
      if (this.redeem !== undefined && this.redeem.length !== 0) {
        this.disableRedeem = true
        this.disableAmount = true
        this.redeem_type = this.redeem.name
        this.amount_redeem = this.redeem.eq_points
      } else if (this.redeem === undefined || this.redeem.length === 0) {
        this.redeem_type = 'CASH'
        this.disableRedeem = true
        // this.disableAmount = false
        this.amount_redeem = ''
      } else {
        this.disableRedeem = false
      }
    },
    goBack () {
      this.active = 1
      this.clear_form()
      this.scanner = true
    },
    submitForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          const tax = (this.amount_redeem / 1.12 * 0.01).toFixed(2)
          const form = {
            customer_id: this.filteredCustomer[0].id,
            redeem_type: this.redeem_type,
            amount: (this.amount_redeem - tax).toFixed(2),
            serial_no: this.serial_number,
            points: this.customer_redeem === '' ? this.filteredCustomer[0].points - this.amount_redeem : 0,
            tax: tax,
            shift: this.shift.shift,
            username: this.user.username,
            customer_redeem: this.customer_redeem
          }
          this.addPointsRedeem(form)
        }
      })
    },
    async addPointsRedeem (form) {
      const point = this.adminPoints ? parseFloat(this.pointsCustomer) : parseFloat(this.fleetPoints)
      if (point >= parseFloat(form.amount)) {
        this.$store.commit('appUI/setIsLoading', true)
        this.$store.commit('appUI/setIsDisable', true)
        this.$store.dispatch('pointsRedeem/addPointsRedeem', form)
          .then(() => {
            this.$store.commit('appUI/setIsLoading', false)
            this.$store.commit('appUI/setIsDisable', false)
            this.currentPoints = form
            this.availablePoints = this.customer_redeem === '' ? (this.currentPoints.points).toFixed(2) : 0
            this.$store.commit('pointsRedeem/setSelectedPointsRedeem')
            this.getRedeemPrint(form)
            this.fleetPoints = 0
            this.adminPoints = false
            this.next()
            this.clear_form()
          })
          .catch(errors => {
            const serial = this.redeemPoints.find(el => el.serial_no === this.serial_number)
            if (serial === undefined) {
              this.$notify.error({
                title: 'Error',
                message: 'Single Redeem Only per Day!',
                duration: 2000
              })
            } else {
              this.$notify.error({
                title: 'Error',
                message: 'Serial Number is already Taken!',
                duration: 2000
              })
            }
          })
      } else if (parseFloat(point) < parseFloat(form.amount)) {
        this.$notify({
          title: 'Error',
          message: 'You dont have enough points to redeem!',
          type: 'error'
        })
      } else {
        this.$notify({
          title: 'Error',
          message: 'Points Must be Greater than 60 before you can Redeem',
          type: 'error'
        })
      }
    },
    async onSubmit () {
      if (this.birthday === this.filteredCustomer[0].birthdate) {
        this.active = 1
        this.next()
        this.redeemDialog = false
        this.checkFleet()
        this.clear_form()
      } else {
        this.$notify.error({
          title: 'Error',
          message: 'Wrong Birthday!',
          duration: 2000
        })
      }
    },
    next () {
      if (this.active++ > 2) this.active = 0
    },
    clear_form: function () {
      this.barcode = ''
      this.birthday = ''
      this.redeem_type = ''
      this.amount_redeem = ''
      this.serial_number = ''
    },
    onCancel () {
      this.redeemDialog = false
    },
    handleClose (done) {
      this.onCancel()
    },
    onBarcodeScanned (barcode) {
      if (this.scanner) {
        this.barcode = barcode
      } else {
        this.$notify({
          title: 'Error',
          message: 'Please finish the transaction first or reload the page!',
          type: 'error',
          duration: 2000
        })
      }
    },
    resetBarcode () {
      let barcode = this.$barcodeScanner.getPreviousCode()
      console.log(barcode)
    },
    handleRedeem (item) {
      this.redeemInfo = Object.assign({}, item)
      this.redeem_type = this.redeemInfo.name
      this.amount_redeem = this.redeemInfo.eq_points
      this.disableAmount = true
      this.disableRedeem = true
      // this.$store.commit('pointsRedeem/setSelectedPointsRedeem', this.redeemInfo)
    }
  },
  watch: {
    barcode () {
      this.onInputBarcode()
    }
  }
}
</script>
<style scoped>
/* .el-card__header {
  background-color: #51b156;
} */
.note {
  font-family: unset;
  font-size: 17px;
}
.barcodeheight {
  height: 100px;
}
.points {
  font-size: 65px;
}
.text {
    font-size: 14px;
  }
  .item {
    margin-bottom: 18px;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
</style>



