export default {
  getPromos (payload = {}) {
    return axios.get(process.env.API_ENDPOINT + '/promo-item?points=' + payload.points)
  },
  addPromo (payload = {}) {
    return axios.post(process.env.API_ENDPOINT + '/promo-item', payload)
  },
  updatePromo (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.API_ENDPOINT + `/promo-item/${payload.id}`, payload)
  },
  deletePromo (payload = {}) {
    return axios.delete(process.env.API_ENDPOINT + `/promo-item/${payload.id}`, payload)
  }
}



