<template>
  <div>
    <app-modal
      :title="title"
      :dialogVisible="deleteDialog"
      :loading="isLoading"
      :disabled="isDisable"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit">
      <div slot="body">
        Are you sure you want to delete?
      </div>
    </app-modal>
    <el-dialog
      title="Fleet Card"
      :visible.sync="fleetDialog"
      width="50%"
      :before-close="handleClose">
      <el-form label-width="140px" label-position="left">
        <el-form-item label="Company Name:">
          <el-input v-model="form.company_name" placeholder="Input Company Name"></el-input>
        </el-form-item>
        <el-form-item label="Company Number:">
          <el-input v-model="form.company_number" placeholder="Input Company Number" type="number"></el-input>
        </el-form-item>
        <el-form-item label="Company Address:">
          <el-input v-model="form.company_address" placeholder="Input Company Address"></el-input>
        </el-form-item>
        <el-form-item label="Tin No:">
          <el-input v-model="form.tin_no" placeholder="Input Tin No."></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">Cancel</el-button>
        <el-button type="primary" @click="fleetDialog = false">Confirm</el-button>
      </span>
    </el-dialog>
    <el-card shadow="never">
      <div slot="header" class="clearfix">
        <span><i class="fa fa-plus"></i> Add Group</span>
      </div>
      <el-form :model="ruleForm" label-position="left" label-width="100px">
      <el-row :gutter="24">
        <el-col :span="12">
          <el-form-item label="Name">
            <el-input name="Name" :class="errors.has('Name')? 'is-error':''" v-validate="'required'" v-model="ruleForm.name" placeholder="Enter Name"></el-input>
            <span v-show="errors.has('Name')" class="text-error">{{ errors.first('Name') }}</span>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="Description">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4}"
              placeholder="Enter Description"
              v-model="ruleForm.description">
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="Fleet Card:">
        <el-checkbox v-model="isFleet" @change="fleetCard"></el-checkbox>
      </el-form-item>
      <el-button style="float: right" v-if="isEdit" :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> UPDATE</el-button>
      <el-button style="float: right" v-else :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> SAVE</el-button>
      <br><br>
      </el-form>
    </el-card>
    <br><br><br>
    <el-card shadow="never">
      <div slot="header" class="clearfix">
        <span><i class="fa fa-list"></i> List of Customer Group</span>
      </div>
      <el-table
        :data="customerGroups"
        style="width: 100%"
        border>
        <el-table-column
          prop="name"
          label="Name"
          width="300">
        </el-table-column>
        <el-table-column
          prop="description"
          label="Description">
        </el-table-column>
        <el-table-column
          label="Action">
          <template slot-scope="scope">
            <el-button
              icon="fa fa-pencil"
              title="Edit Customer Information"
              type="success"
              circle
              @click="handleEdit(scope.row)">
            </el-button>
            <!-- <el-button
              icon="fa fa-trash"
              title="Delete Customer Information"
              class="colorPrimary"
              circle
              @click="handleDelete(scope.row)">
            </el-button> -->
          </template>
        </el-table-column>
      </el-table>
      <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
      </app-pagination>
    </el-card>
  </div>
</template>

<script>

import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
import Modal from '@/components/Modal'

export default {
  data () {
    return {
      isFleet: false,
      isEdit: false,
      form: {
        company_name: '',
        company_number: '',
        company_address: '',
        tin_no: ''
      },
      ruleForm: {
        description: '',
        name: ''
      },
      title: '',
      deleteDialog: false,
      fleetDialog: false,
      groupInfo: [],
      page: 1
    }
  },
  computed: {
    ...mapGetters('user', {
      users: 'users'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('customerGroup', {
      customerGroups: 'customerGroups',
      pagination: 'pagination',
      response: 'response'
    })
  },
  watch: {
    page () {
      this.getCustomerGroup()
    }
  },
  created () {
    this.getCustomerGroup()
  },
  methods: {
    fleetCard () {
      this.fleetDialog = true
    },
    getCustomerGroup () {
      const page = this.page
      this.$store.dispatch('customerGroup/getCustomerGroup', { page })
    },
    clear_form () {
      this.ruleForm = {
        name: '',
        description: ''
      }
      this.form = {
        company_name: '',
        company_number: '',
        company_address: '',
        tin_no: ''
      }
      this.isFleet = false
      this.$nextTick(() => {
        this.$validator.reset()
      })
    },
    submitForm () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (this.isEdit) {
            this.updateCustomerGroup(this.ruleForm)
          } else {
            this.addCustomerGroup()
          }
        }
      })
    },
    handleEdit (item) {
      this.isEdit = true
      this.ruleForm = Object.assign({}, item)
    },
    handleDelete (item) {
      this.deleteDialog = true
      this.title = 'Delete'
      this.groupInfo = Object.assign({}, item)
    },
    async addCustomerGroup () {
      const form = {
        name: this.ruleForm.name,
        description: this.ruleForm.description,
        fleet_card: this.form,
        fleet: this.isFleet
      }
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('customerGroup/addCustomerGroup', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.customerGroups.unshift(this.response)
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async updateCustomerGroup (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('customerGroup/updateCustomerGroup', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Successfully Updated!',
            type: 'success',
            duration: 2000
          })
          this.getCustomerGroup()
          this.isEdit = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async onSubmit () {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      await this.$store.dispatch('customerGroup/deleteCustomerGroup', this.groupInfo)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'SuccessFully Deleted!',
            type: 'success',
            duration: 2000
          })
          const index = this.customerGroups.findIndex(value => value.id === this.groupInfo.id)
          this.customerGroups.splice(index, 1)
          this.deleteDialog = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify.error({
            title: 'Error',
            message: 'Something went wrong!',
            duration: 2000
          })
          throw new Error(error)
        })
    },
    onCancel () {
      this.deleteDialog = false
      this.fleetDialog = false
      this.isFleet = false
      this.form = {
        company_name: '',
        company_number: '',
        company_address: '',
        tin_no: ''
      }
    },
    handleClose (done) {
      this.onCancel()
    },
    handlePagination (page) {
      this.page = page
    }
  },
  components: {
    'app-pagination': Pagination,
    'app-modal': Modal
  }
}
</script>

<style scoped>
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.bg-purple {
  background: #99a9bf;
}
.clearfix {
  font-size: 18px;
}
.el-button.is-circle {
  border-radius: 50%;
  padding: 5px;
  margin: 3px;
}
</style>



