export default {
  getCustomers (payload = {}) {
    const params = {
      s: payload.s,
      s_field: payload.s_field,
      page: payload.page,
      export: payload.export,
      type: payload.type,
      customer_group: payload.customer_group,
      from_date: payload.from_date,
      limit: 15
    }
    return axios.get(process.env.API_ENDPOINT + '/customer', { params })
  },
  addCustomer (payload = {}) {
    return axios.post(process.env.API_ENDPOINT + '/customer', payload)
  },
  updateCustomer (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.API_ENDPOINT + `/customer/${payload.id}`, payload)
  },
  deleteCustomer (payload = {}) {
    return axios.delete(process.env.API_ENDPOINT + `/customer/${payload.id}`, payload)
  },
  getCustomerHigh (payload = {}) {
    return axios.get(process.env.API_ENDPOINT + '/customer-high?filter_customer_group=' + payload.customer_group + '&limit=' + payload.limit + '&type=' + payload.type + '&points=' + payload.points)
  }
}



