<template>
  <div>
    <app-modal
      :title="title"
      :dialogVisible="deleteDialog"
      :loading="isLoading"
      :disabled="isDisable"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit"
      >
      <div slot="body">
        Are you sure you want to delete?
      </div>
    </app-modal>
    <app-point-modal
      :dialogVisible="highPoints"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @submitTagPromo="submitTagPromo">
      <div slot="body">
        <el-row :gutter="24">
          <el-col :span="14">
            <el-form>
              <span class="demo-input-label" style="width: auto;">Customer Group: &nbsp;</span>
              <el-select clearable v-model="selectGroup" @change="selectGroupEvent" placeholder="Filter by Customer Group">
                <el-option
                  v-for="item in customerGroups"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form>
          </el-col>
          <el-col :span="10">
            <el-form label-width="110px">
              <el-form-item label="Top Customer:">
                <el-select @change="selectLimitOnly" clearable v-model="selectLimit" placeholder="----- SELECT -----">
                  <el-option label="Top 1" value="1"></el-option>
                  <el-option label="Top 5" value="5"></el-option>
                  <el-option label="Top 10" value="10"></el-option>
                  <el-option label="Top 15" value="15"></el-option>
                  <el-option label="Top 20" value="20"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
        <el-row :gutter="24" v-if="showTag">
          <el-col :span="24">
            <el-form label-width="120px">
              <el-form-item label="Select Promo: ">
                <el-select style="width: 100%"  @change="pointType()" v-model="ruleForm.promo_id" placeholder="Promo Items" name="Customer Group" v-validate="'required'">
                  <el-option
                    v-for="item in promos"
                    :key="item.id"
                    :label="item.name + ' ' +'( '+ item.point_types + ' )'"
                    :value="item.id">
                  </el-option>
                </el-select>
                <span class="text-error">{{ errors.first('Customer Group') }}</span>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
        <el-alert title="Please Select Promo Items !" type="error" /><br>
        <el-alert title="Disabled checkboxes are already tagged !" type="success" /><br>
        <el-table
        width="100%"
        :data="customerHighPoints"
        border
        :row-class-name="tableRowClassName"
        @selection-change="onTagCustomer"
        >
        <el-table-column
        type="selection"
        :selectable="canSelectRow">
        </el-table-column>
        <el-table-column
        label="Customer Name">
        <template slot-scope="scope">
          {{ showType === 'running' ? scope.row.first_name + ' ' + scope.row.last_name : scope.row.first_name + ' ' + scope.row.last_name }}
        </template>
        </el-table-column>
        <el-table-column
        prop="points"
        label="Points">
        </el-table-column>
        <el-table-column
        label="Contact No.">
        <template slot-scope="scope">
          {{ showType === 'running' ? scope.row.mobile_no : scope.row.mobile_no }}
        </template>
        </el-table-column>
        <el-table-column
        label="Customer Group">
        <template slot-scope="scope">
          {{ getGroup(scope.row.customer_group_id) }}
        </template>
        </el-table-column>
        </el-table>
      </div>
    </app-point-modal>
    <el-row v-show="showAdd">
      <el-card class="box-card" shadow="never">
        <template>
          <div slot="header" class="clearfix">
            <span id="span_header_2"><b>ADD PROMO ITEM</b></span>
          </div>
          <el-form label-position="left" :model="ruleForm" label-width="100px">
            <el-row :gutter="24">
              <el-col :span="12">
                <el-form-item label="Promo Name:">
                  <el-input name="Promo" :class="errors.has('Promo')? 'is-error':''" v-validate="'required'" v-model="ruleForm.name" placeholder="Input Promo Name"></el-input>
                  <span v-show="errors.has('Promo')" class="text-error">This field is required</span>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="Date Start:">
                  <el-date-picker name="DateStart" :class="errors.has('DateStart')? 'is-error':''" v-validate="'required'" placeholder="Pick a date" v-model="ruleForm.date_start" style="width: 100%;"></el-date-picker>
                  <span v-show="errors.has('DateStart')" class="text-error">{{ errors.first('DateStart') }}</span>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="12">
                <el-form-item label="Points:">
                  <el-input type="number" name="Points" :class="errors.has('Points')? 'is-error':''" v-validate="'required'" v-model="ruleForm.eq_points" placeholder="Input Equivalent Points"></el-input>
                  <span v-show="errors.has('Points')" class="text-error">This field is required</span>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="Date End:">
                  <el-date-picker name="DateEnd" :class="errors.has('DateEnd')? 'is-error':''" v-validate="'required'" placeholder="Pick a date" v-model="ruleForm.date_end" style="width: 100%;"></el-date-picker>
                  <span v-show="errors.has('DateEnd')" class="text-error">{{ errors.first('DateEnd') }}</span>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="12">
                <el-form-item label="Points Type:">
                  <el-select v-model="ruleForm.point_types" placeholder="Select" style="width: 100%">
                    <el-option label="Running Points" value="running"></el-option>
                    <el-option label="Current Points" value="current"></el-option>
                  </el-select>
                  <span v-show="errors.has('Promo')" class="text-error">This field is required</span>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item>
              <el-button style="float: right" v-if="isEdit" :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> UPDATE</el-button>
              <el-button style="float: right" v-else :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> SAVE</el-button>
            </el-form-item>
          </el-form>
        </template>
      </el-card>
    </el-row>
    <br>
    <el-row>
      <el-card class="box-card" shadow="never">
        <template>
          <div slot="header" class="clearfix">
            <span id="span_header_2"><b>LIST OF PROMOS</b><el-button style="float: right;margin-top: -11px" class="submit" @click="handleHighPoints"><b>TOP CUSTOMERS</b></el-button></span>
          </div>
            <el-table
            :data="promos"
            border>
            <el-table-column
              label="NAME"
              prop="name"
              fixed>
            </el-table-column>
            <el-table-column
              prop="eq_points"
              width="200"
              label="EQUIVALENT POINTS">
            </el-table-column>
            <el-table-column
              label="POINT TYPE">
            <template slot-scope="scope">
              {{ scope.row.point_types === 'current' ? 'CURRENT POINTS' : 'RUNNING POINTS'}}
            </template>
            </el-table-column>
            <el-table-column
              prop="date_start"
              label="DATE START">
            </el-table-column>
            <el-table-column
              prop="date_end"
              label="DATE END">
            </el-table-column>
            <el-table-column
              prop="liters"
              v-if="showAdd"
              label="ACTION">
              <template slot-scope="scope">
                <el-button
                  icon="fa fa-pencil"
                  title="Edit Customer Information"
                  type="success"
                  @click="handleEdit(scope.row)"
                  circle>
                </el-button>
                <el-button
                  icon="fa fa-trash"
                  title="Delete Customer Information"
                  class="colorPrimary"
                  @click="handleDelete(scope.row)"
                  circle>
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </el-card>
    </el-row>
  </div>
</template>

<script>
import CustomerHighPointsModal from '@/components/CustomerHighPointsModal'
import Modal from '@/components/Modal'
import moment from 'moment'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      points: '',
      keyCheck: true,
      show: true,
      showType: '',
      selection: 'selection',
      user: '',
      showTag: '',
      element: '',
      filtered: '',
      selectedCustomer: [],
      selectLimit: '',
      count: '',
      selectGroup: '',
      highPoints: false,
      showAdd: false,
      deleteDialog: false,
      isEdit: false,
      ruleForm: {
        date_start: Date.now(),
        date_end: '',
        eq_points: '',
        point_types: '',
        name: '',
        id: ''
      },
      promoInfo: [],
      title: ''
    }
  },
  computed: {
    ...mapGetters('promoItems', {
      promos: 'promos',
      response: 'response'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('customer', {
      customerHighPoints: 'customerHighPoints'
    }),
    ...mapGetters('customerGroup', {
      customerGroups: 'customerGroups'
    }),
    ...mapGetters('transaction', {
      transactions: 'transactions'
    }),
    ...mapGetters('promoCustomerAvail', {
      tagCustomers: 'tagCustomers'
    })
  },
  created () {
    this.getPromos()
    this.getCurrentUser()
    // this.getCustomerHigh()
    this.getCustomerGroup()
    this.getPromoCustomer()
    this.getTagCustomers()
    this.getUser()
  },
  methods: {
    tableRowClassName ({row, rowIndex}) {
      const tag = this.tagCustomers.filter(el => el.customer_id === row.id)
      if (tag.length > 0 && !tag[0].tag) {
        const index = rowIndex
        if (rowIndex === index && !tag[0].tag) {
          return 'success-row'
        } else {
          return ''
        }
      } else {
        return ''
      }
    },
    canSelectRow (row, index) {
      const tag = this.tagCustomers.filter(el => el.customer_id === row.id)
      if (tag.length > 0 && !tag[0].tag) {
        return false
      } else {
        return true
      }
    },
    pointType () {
      const promo = this.promos.filter(el => el.id === this.ruleForm.promo_id)
      const type = promo[0].point_types
      this.showType = type
      const points = promo[0].eq_points
      this.points = points
      this.$store.dispatch('customer/getCustomerHigh', {
        customer_group: this.selectGroup ? this.selectGroup : 0,
        limit: this.selectLimit ? this.selectLimit : 0,
        type: type,
        points: points
      })
    },
    getUser () {
      const users = localStorage.getItem('users')
      this.user = JSON.parse(users)
      const user = this.user.roles.data[0].name
      if (user === 'admin') {
        this.showTag = true
      } else {
        this.showTag = false
      }
    },
    getPromoCustomer () {
      this.$store.dispatch('promoCustomerAvail/getPromoCustomer')
      // this.condition = this.switch
    },
    getTagCustomers () {
      this.$store.dispatch('promoCustomerAvail/getTagCustomers')
    },
    onTagCustomer (row) {
      this.selectedCustomer = row
    },
    submitTagPromo () {
      if (this.ruleForm.promo_id !== undefined) {
        const promoExpired = this.promos.find(el => el.id === this.ruleForm.promo_id)
        for (let index = 0; index < this.selectedCustomer.length; index++) {
          this.element = this.selectedCustomer[index]
          const form = {
            customer_id: this.element.id,
            promo_id: this.ruleForm.promo_id,
            isAvail: 'false',
            date_expired: promoExpired.date_end
          }
          this.filtered = form
          this.$store.dispatch('promoCustomerAvail/addPromoCustomer', this.filtered)
        }
        this.getPromoCustomer()
        this.$notify({
          title: 'Success',
          message: 'Promo Successfully Tagged To Customer',
          type: 'success',
          duration: 2000
        })
        this.highPoints = false
        this.selectedCustomer = []
        this.ruleForm.promo_id = ''
        this.selectLimit = ''
        this.getCustomerHigh()
        setTimeout(function () {
          window.location.reload()
        }, 1000)
      } else {
        this.$notify({
          title: 'Error',
          message: 'Please Select Promo Item!',
          type: 'error',
          duration: 2000
        })
      }
    },
    getGroup (row) {
      const group = this.customerGroups.find(el => el.id === row)
      if (group) {
        return group.name
      } else {
        return 'No Group'
      }
    },
    selectGroupEvent () {
      this.$store.dispatch('customer/getCustomerHigh', {
        customer_group: this.selectGroup ? this.selectGroup : 0,
        type: this.showType,
        points: this.points,
        limit: this.selectLimit ? this.selectLimit : 0
      })
    },
    selectLimitOnly () {
      this.$store.dispatch('customer/getCustomerHigh', {
        customer_group: this.selectGroup ? this.selectGroup : 0,
        type: this.showType,
        points: this.points,
        limit: this.selectLimit
      })
    },
    getCurrentUser () {
      const users = localStorage.getItem('users')
      this.user = JSON.parse(users)
      const user = this.user.roles.data[0].name
      if (user === 'admin') {
        this.showAdd = true
      } else {
        this.showAdd = false
      }
    },
    getPromos () {
      this.$store.dispatch('promoItems/getPromos', {points: 0})
    },
    getCustomerHigh () {
      this.$store.dispatch('customer/getCustomerHigh', {customer_group: 0, limit: 0})
    },
    getCustomerGroup () {
      this.$store.dispatch('customerGroup/getCustomerGroup', { page: null })
    },
    submitForm () {
      const form = {
        id: this.ruleForm.id,
        date_start: moment(this.ruleForm.date_start).format('YYYY-MM-DD'),
        date_end: moment(this.ruleForm.date_end).format('YYYY-MM-DD'),
        name: this.ruleForm.name,
        eq_points: this.ruleForm.eq_points,
        point_types: this.ruleForm.point_types
      }
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (this.isEdit) {
            this.updatePromo(form)
          } else {
            this.addPromo(form)
          }
        }
      })
    },
    async addPromo (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('promoItems/addPromo', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Promo Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.promos.unshift(this.response)
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async updatePromo (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('promoItems/updatePromo', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Promo Successfully Updated!',
            type: 'success',
            duration: 2000
          })
          this.getPromos()
          this.isEdit = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async onSubmit () {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      await this.$store.dispatch('promoItems/deletePromo', this.promoInfo)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Promo SuccessFully Deleted!',
            type: 'success',
            duration: 2000
          })
          const index = this.promos.findIndex(value => value.id === this.promoInfo.id)
          this.promos.splice(index, 1)
          this.deleteDialog = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify.error({
            title: 'Error',
            message: 'Something went wrong!',
            duration: 2000
          })
          throw new Error(error)
        })
    },
    onCancel () {
      this.deleteDialog = false
      this.highPoints = false
    },
    clear_form: function () {
      this.ruleForm = {
        date_start: Date.now(),
        date_end: '',
        eq_points: '',
        name: '',
        id: ''
      }
      this.$nextTick(() => {
        this.$validator.reset()
      })
    },
    handleEdit (item) {
      this.isEdit = true
      this.ruleForm = Object.assign({}, item)
      this.ruleForm.id = item.id
    },
    handleDelete (item) {
      this.deleteDialog = true
      this.title = 'Delete'
      this.promoInfo = Object.assign({}, item)
    },
    handleHighPoints () {
      this.highPoints = true
    },
    handleClose (done) {
      this.onCancel()
    }
  },
  components: {
    'app-modal': Modal,
    'app-point-modal': CustomerHighPointsModal
  }
}
</script>
<style>
.el-button.is-circle {
  border-radius: 50%;
  padding: 5px;
  margin: 3px;
}
.el-table .warning-row {
    background: oldlace;
  }

.el-table .success-row {
  background: #dafcc8;
}
</style>



