import api from '../api/promoCustomerAvail'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    promoCustomers: [],
    tagCustomers: [],
    pagination: ''
  },
  getters: {
    promoCustomers: state => state.promoCustomers,
    tagCustomers: state => state.tagCustomers,
    pagination: state => state.pagination
  },
  mutations: {
    [types.mutations.promoCustomerAvail.setPromoCustomer] (state, payload) {
      state.promoCustomers = payload
    },
    [types.mutations.promoCustomerAvail.setTagCustomer] (state, payload) {
      state.tagCustomers = payload
    }
  },
  actions: {
    async [types.actions.promoCustomerAvail.getPromoCustomer] ({ commit }, payload) {
      try {
        const response = await api.getPromoCustomer(payload)
        commit(types.mutations.promoCustomerAvail.setPromoCustomer, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.promoCustomerAvail.getTagCustomers] ({ commit }, payload) {
      try {
        const response = await api.getTagCustomers(payload)
        commit(types.mutations.promoCustomerAvail.setTagCustomer, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.promoCustomerAvail.addPromoCustomer] ({ commit }, payload) {
      try {
        await api.addPromoCustomer(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.promoCustomerAvail.updatePromoCustomer] (state, payload) {
      try {
        await api.updatePromoCustomer(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}



