<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="10">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-edit"></i> <b>ADD LITERS</b></span>
          </div>
          <el-form :model="ruleForm" label-width="140px">
            <el-form-item label="SELECT DATE">
              <el-date-picker
                style="width: 100%"
                name="Date"
                v-model="ruleForm.date"
                type="date"
                placeholder="Pick a day"
                v-validate="'required'">
              </el-date-picker>
              <span class="text-error">{{ errors.first('Date') }}</span>
            </el-form-item>
            <el-form-item label="ENTER PREMIUM">
              <el-input type="number" placeholder="Please input" v-model="ruleForm.premium" name="Price" v-validate="'required'"></el-input>
            </el-form-item>
            <el-form-item label="ENTER UNLEADED">
              <el-input type="number" placeholder="Please input" v-model="ruleForm.unleaded" name="Price" v-validate="'required'"></el-input>
            </el-form-item>
            <el-form-item label="ENTER DIESEL">
              <el-input type="number" placeholder="Please input" v-model="ruleForm.desiel" name="Price" v-validate="'required'"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button style="float: right" v-if="isEdit" :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> UPDATE</el-button>
              <el-button style="float: right"  v-else :loading="isLoading" :disabled="isDisable" type="submit" class="submit" icon="fa fa-floppy-o" @click="submitForm"> SAVE</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
      <el-col :span="14">
        <el-card shadow="never">
          <div slot="header" class="clearfix">
            <span><i class="fa fa-list"></i> <b>TOTAL STATION VOLUME IN LITERS</b></span>
          </div>
          <el-table
            :data="generate_templates"
            border
            style="width: 100%">
            <el-table-column
              prop="date"
              label="DATE">
            </el-table-column>
            <el-table-column
              prop="premium"
              label="PREMIUM">
            </el-table-column>
            <el-table-column
              prop="unleaded"
              label="UNLEADED">
            </el-table-column>
            <el-table-column
              prop="desiel"
              label="DIESEL">
            </el-table-column>
            <el-table-column
              label="Action">
              <template slot-scope="scope">
                <el-button
                  icon="fa fa-pencil"
                  title="Edit Customer Information"
                  type="success"
                  circle
                  @click="handleEdit(scope.row)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
         </app-pagination>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import Pagination from '@/components/Pagination'
import moment from 'moment'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      page: 1,
      isEdit: false,
      ruleForm: {
        premium: '',
        unleaded: '',
        desiel: '',
        date: Date.now()
      }
    }
  },
  computed: {
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('generateTemplate', {
      generate_templates: 'generate_templates',
      pagination: 'pagination'
    }),
    totalamount: function () {
      const premium = parseInt(this.premium)
      const unleaded = parseInt(this.unleaded)
      const sum = premium + unleaded
      return parseFloat(sum)
    }
  },
  watch: {
    page () {
      this.getGenerateTemplate()
    }
  },
  created () {
    this.getGenerateTemplate()
  },
  methods: {
    submitForm () {
      const total = 12
      const form = {
        id: this.ruleForm.id,
        date: moment(this.ruleForm.date).format('YYYY-MM-DD'),
        premium: this.ruleForm.premium,
        unleaded: this.ruleForm.unleaded,
        desiel: this.ruleForm.desiel,
        total: total
      }
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (this.isEdit) {
            this.updateGenerateTemplate(form)
          } else {
            this.addGenerateTemplate(form)
          }
        }
      })
    },
    async addGenerateTemplate (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('generateTemplate/addGenerateTemplate', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Successfully Saved!',
            type: 'success',
            duration: 2000
          })
          this.getGenerateTemplate()
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Date Already Exist!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    async updateGenerateTemplate (form) {
      this.$store.commit('appUI/setIsLoading', true)
      this.$store.commit('appUI/setIsDisable', true)
      this.$store.dispatch('generateTemplate/updateGenerateTemplate', form)
        .then(() => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Success',
            message: 'Template Successfully Updated!',
            type: 'success',
            duration: 2000
          })
          this.getGenerateTemplate()
          this.isEdit = false
        })
        .catch(error => {
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
      this.clear_form()
    },
    getGenerateTemplate () {
      this.$store.dispatch('generateTemplate/getGenerateTemplate', {page: this.page})
    },
    clear_form () {
      this.ruleForm = {
        date: Date.now(),
        premium: '',
        unleaded: '',
        desiel: ''
      }
    },
    handleEdit (item) {
      this.isEdit = true
      this.ruleForm = Object.assign({}, item)
    },
    handlePagination (page) {
      this.page = page
    }
  },
  components: {
    'app-pagination': Pagination
  }
}
</script>
<style scoped>
.el-button.is-circle {
  border-radius: 50%;
  padding: 5px;
  margin: 3px;
}
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.clearfix {
  font-size: 18px;
}
.el-select {
  width: 100%;
}
.el-date-editor.el-input {
  width: 100%;
}
</style>



