import api from '../api/generateTemplate'
import types from '../../types'
export default {
  namespaced: true,
  state: {
    generate_templates: [],
    pagination: '',
    response: []
  },
  getters: {
    generate_templates: state => state.generate_templates,
    pagination: state => state.pagination,
    response: state => state.response
  },
  mutations: {
    [types.mutations.generateTemplate.setGenerateTemplate] (state, payload) {
      if (payload.data === undefined) {
        state.generate_templates = payload
        state.pagination = payload
      } else {
        state.generate_templates = payload.data
        state.pagination = payload
      }
    }
  },
  actions: {
    async [types.actions.generateTemplate.getGenerateTemplate] ({ commit }, payload) {
      try {
        const response = await api.getGenerateTemplate(payload)
        commit(types.mutations.generateTemplate.setGenerateTemplate, response.data)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.generateTemplate.addGenerateTemplate] ({ commit }, payload) {
      try {
        await api.addGenerateTemplate(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.generateTemplate.updateGenerateTemplate] (state, payload) {
      try {
        await api.updateGenerateTemplate(payload)
      } catch (error) {
        throw new Error(error)
      }
    },
    async [types.actions.generateTemplate.deleteGenerateTemplate] (state, payload) {
      try {
        await api.deleteGenerateTemplate(payload)
      } catch (error) {
        throw new Error(error)
      }
    }
  }
}



