<template>
    <div>
      <app-modal
      :dialogVisible="renewDialog"
      :loading="isLoading"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onSubmit="onSubmit"
      >
      <div slot="body">
        Are you sure you want to renew this customer?
      </div>
      </app-modal>
      <app-redeem-modal
      :dialogVisible="promoCustomerAvailDialog"
      :loading="isLoading"
      @handleClose="handleClose"
      @onCancel="onCancel"
      @onRedeem="onRedeem"
      >
      <div slot="body">
        <el-form label-width="120px">
          <el-form-item label="Serial Number" prop="checkPass">
            <el-input name="Serial Number" style="width: 100%" v-model="serial_number" :class="errors.has('Serial Number')? 'is-error':''" v-validate="'required'" placeholder=" Enter Serial Number" auto-complete="off"></el-input>
            <span v-show="errors.has('Serial Number')" class="text-error">{{ errors.first('Serial Number') }}</span>
          </el-form-item>
        </el-form>
      </div>
      </app-redeem-modal>
        <center>
            <br><br>
            <el-row :gutter="20">
                <el-col :span="20" :offset="2">
                    <h1 v-if="condition">Tap your Card Now</h1>
                    <el-input v-if="inputField" name="code" @input="onInputBarcode" v-model="barcode" autofocus class="input code" placeholder="Please Tap Your Card To Inquire..."></el-input>
                </el-col>
            </el-row>
        </center>
        <div>
            <br><br><br>
            <fieldset v-show="showCustomer">
            <legend><strong>Customer Details</strong></legend>
            <div style="margin-left: 60%;margin-top: 28px;"><p v-show="displayResult.first_name">REMAINING POINTS: <br></p>&nbsp; <b class="points-available">{{fleetPoints === '' ? displayResult.points : fleetPoints}}</b></div>
                <el-col><br>
                    <el-table
                    :data="filteredCustomer"
                    border
                    style="width: 100%">
                    <el-table-column
                    label="CUSTOMER NAME">
                    <template slot-scope="scope">
                    {{ scope.row.first_name + ' ' + scope.row.last_name}}
                    </template>
                    </el-table-column>
                    <el-table-column
                    prop="address"
                    label="ADDRESS">
                    </el-table-column>
                    <el-table-column
                    prop="customer_group_id"
                    label="CUSTOMER GROUP">
                    <template slot-scope="scope">
                        {{ scope.row.customer_group.name}}
                    </template>
                    </el-table-column>
                    <el-table-column
                    prop="vehicles"
                    label="VEHICLE">
                    </el-table-column>
                    <el-table-column
                    prop="mobile_no"
                    label="MOBILE NUMBER">
                    </el-table-column>
                    </el-table>
                  <br>
                  <div class="remain">
                    <p>REMAINING:&nbsp;&nbsp;<b>{{remainingDays}}</b>&nbsp;&nbsp;&nbsp;&nbsp;<el-button @click="renewExpiration" size="mini" v-if="showAdd" type="success">RENEW</el-button></p>
                  </div>
                </el-col>
            </fieldset>
            <br><br>
            <el-row v-show="showPromo">
              <el-tabs type="border-card">
              <el-tab-pane label="Transactions">
                <el-row>
                  <el-button @click="exportExcel" size="medium" class="tile-yellow" style="float: right">Export to Excel</el-button>
                  <br><br><br><br>
                  <el-table border :data="transactions">
                    <el-table-column
                      prop="amount"
                      label="Amount">
                    </el-table-column>
                    <el-table-column
                      prop="liters"
                      label="Liters">
                    </el-table-column>
                    <el-table-column
                      prop="points"
                      label="Points">
                    </el-table-column>
                    <el-table-column
                      label="Date"
                      prop="created_at">
                    <template slot-scope="scope">
                      {{ toDateString(scope.row.created_at) }}
                    </template>
                    </el-table-column>
                  </el-table>
                <app-pagination :pageSize="parseInt(pagination_transactions.per_page)" :total="pagination_transactions.total" @current-change="handlePagination">
                </app-pagination>
                </el-row>
              </el-tab-pane>
              <el-tab-pane label="Redeem">
                <el-button @click="exportExcelRedeem" size="medium" class="tile-yellow" style="float: right">Export to Excel</el-button>
                <br><br><br><br>
                <el-table border :data="redeemPoints">
                  <el-table-column
                    prop="amount"
                    label="Amount">
                  </el-table-column>
                  <el-table-column
                    prop="redeem_type"
                    label="Redeem Type">
                  </el-table-column>
                  <el-table-column
                    prop="serial_no"
                    label="Serial No.">
                  </el-table-column>
                  <el-table-column
                    label="Date"
                    prop="created_at">
                  <template slot-scope="scope">
                    {{ toDateString(scope.row.created_at) }}
                  </template>
                  </el-table-column>
                </el-table>
                <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
                </app-pagination>
              </el-tab-pane>
              <el-tab-pane label="Available Promos">
                <el-row>
                  <el-table
                    :data="promoCustomers"
                    border>
                    <el-table-column
                      label="PROMO NAME">
                    <template slot-scope="scope">
                      {{ scope.row.promo_item.name}}
                    </template>
                    </el-table-column>
                    <el-table-column
                      label="PROMO END">
                    <template slot-scope="scope">
                      {{ scope.row.promo_item.date_end}}
                    </template>
                    </el-table-column>
                    <el-table-column
                      label="ACTION">
                      <template slot-scope="scope">
                        <el-button
                          type="success"
                          class="button"
                          @click="redeemPromo(scope.row)"
                          size="mini">
                          REDEEM PROMO
                        </el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </el-row>
              </el-tab-pane>
            </el-tabs>
            </el-row>
        </div>
    </div>
</template>
<script>
import Pagination from '@/components/Pagination'
import PromoCustomerAvailModal from '@/components/PromoCustomerAvailModal'
import RenewModal from '@/components/RenewModal'
import { mapGetters } from 'vuex'
import moment from 'moment'
import _ from 'lodash'
export default {
  data () {
    return {
      page: 1,
      fleetPoints: '',
      customerTransactions: [],
      switch: '',
      user: '',
      shift: '',
      inputField: false,
      showPromo: false,
      promoCustomerAvailDialog: false,
      customerId: '',
      date: new Date(),
      showRedeem: true,
      showAdd: false,
      renewDialog: false,
      renew: '',
      remainingDays: '',
      points: '',
      barcode: '',
      isFiltered: false,
      showCustomer: false,
      redeemInfo: [],
      condition: '',
      selectedRedeem: '',
      serial_number: ''
    }
  },
  created () {
    this.getCustomers()
    this.getCredentials()
    this.getUser()
    this.getSwitch()
    this.getFleetCard()
    this.$barcodeScanner.init(this.onBarcodeScanned)
    this.getPromos()
    this.getCurrentUser()
    this.getRedeem()
    this.getPromoCustomer()
  },
  computed: {
    ...mapGetters('transaction', {
      filteredCustomer: 'transactionByBarcode'
    }),
    ...mapGetters('fleetCard', {
      fleets: 'fleets'
    }),
    ...mapGetters('customer', {
      customers: 'customers'
    }),
    ...mapGetters('promoItems', {
      promos: 'promos'
    }),
    ...mapGetters('appUI', {
      isLoading: 'isLoading',
      isDisable: 'isDisable'
    }),
    ...mapGetters('promoCustomerAvail', {
      promoCustomers: 'promoCustomers'
    }),
    ...mapGetters('transaction', {
      transactions: 'transactions',
      pagination_transactions: 'pagination_transactions'
    }),
    ...mapGetters('pointsRedeem', {
      redeemPoints: 'redeemPoints',
      pagination: 'pagination'
    }),
    displayResult () {
      if (this.filteredCustomer.length === 1) {
        return this.filteredCustomer[0]
      }
      return ''
    }
  },
  methods: {
    handlePagination (page) {
      this.page = page
    },
    getTransactions () {
      const page = this.page
      this.$store.dispatch('transaction/getTransactions', {
        customer_id: this.filteredCustomer[0].id,
        page,
        from_date: 0,
        to_date: 0,
        dateOnly: 0,
        shift: 0,
        export: 0
      })
    },
    getPointsRedeem () {
      const page = this.page
      this.$store.dispatch('pointsRedeem/getPointsRedeem', {
        customer_id: this.filteredCustomer[0].id,
        from_date: 0,
        page,
        to_date: 0,
        shift: 0,
        dateOnly: 0,
        export: 0
      })
    },
    getFleetCard () {
      this.$store.dispatch('fleetCard/getFleetCard', {page: null})
    },
    getCustomers () {
      this.$store.dispatch('customer/getCustomers', {page: null})
    },
    checkFleet () {
      const fleet = this.fleets.filter(el => el.customer_group_id === this.filteredCustomer[0].customer_group_id)
      if (fleet.length > 0) {
        if (this.filteredCustomer[0].is_admin) {
          const customer = this.customers.filter(el => el.customer_group_id === this.filteredCustomer[0].customer_group_id)
          const amountArr = customer.map(el => parseFloat(el.points))
          const reducer = (accumulator, currentValue) => accumulator + currentValue
          const finalPoints = amountArr.reduce(reducer).toFixed(2)
          this.fleetPoints = finalPoints.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
          console.log(this.fleetPoints)
        } else {
          console.log('failed')
        }
      }
    },
    exportExcelRedeem () {
      this.fromDate = 0
      this.toDate = 0
      this.oneDate = 0
      this.customerOnly = 0
      this.shiftOnly = 0
      this.url = process.env.API_ENDPOINT + '/points-redeem?filter_by_customer=' + this.customerOnly + '&filter_customer_date=' + this.oneDate + '&filter_by_shift=' + this.shiftOnly + '&export=0&export_by_customer=' + this.filteredCustomer[0].id
      window.location.href = this.url
    },
    exportExcel () {
      this.fromDate = 0
      this.toDate = 0
      this.oneDate = 0
      this.customerOnly = 0
      this.shiftOnly = 0
      this.url = process.env.API_ENDPOINT + '/transaction?filter_by_customer=' + this.customerOnly + '&filter_by_shift=' + this.shiftOnly + '&export=0&export_by_customer=' + this.filteredCustomer[0].id
      window.location.href = this.url
    },
    getCustomerRedeem () {
      this.$store.dispatch('pointsRedeem/getPointsRedeem', {
        customer_id: this.filteredCustomer[0].id,
        page: 0,
        from_date: 0,
        to_date: 0,
        dateOnly: 0,
        shift: 0,
        export: 0
      })
    },
    getCredentials () {
      const credentials = localStorage.getItem('credentials')
      const credential = JSON.parse(credentials)
      this.shift = credential.shift
    },
    getUser () {
      const user = localStorage.getItem('users')
      this.user = JSON.parse(user)
    },
    getSwitch () {
      const toggle = localStorage.getItem('switch')
      this.switch = JSON.parse(toggle)
      this.condition = this.switch
      if (this.switch === false) {
        this.inputField = true
      } else {
        this.inputField = false
      }
    },
    getRedeem () {
      const user = this.user.roles.data[0].name
      if (user === 'admin' || user === 'sic') {
        this.showRedeem = true
      } else {
        this.showRedeem = false
      }
    },
    getCurrentUser () {
      const user = this.user.roles.data[0].name
      if (user === 'admin') {
        this.showAdd = true
      } else {
        this.showAdd = false
      }
    },
    getFca () {
      const user = this.user.roles.data[0].name
      if (user === 'forecourt_attendant') {
        this.showPromo = false
      } else {
        this.showPromo = true
      }
    },
    onInputBarcode: _.debounce(function (event) {
      // this.barcode = event
      if (this.barcode !== '') {
        this.isFiltered = true
        this.loading = true
        this.$store.commit('appUI/setIsLoading', true)
        this.$store.dispatch('transaction/getCustomerByBarcode', {barcode: 'mg-' + this.barcode})
          .then(() => {
            this.$store.commit('appUI/setIsLoading', false)
            if (this.filteredCustomer.length === 1) {
              this.loading = false
              this.isSuccess = false
              this.showCustomer = true
              this.customerTransactions = this.filteredCustomer[0].customer_transactions
              this.getPromos()
              this.getRemainingDays()
              this.getPromoCustomer()
              this.getFca()
              this.getCustomerRedeem()
              this.checkFleet()
              this.getTransactions()
              this.getCustomerRedeem()
              this.$store.commit('pointsRedeem/setSelectedPointsRedeem')
            } else {
              this.isSuccess = true
              this.$notify({
                title: 'Error',
                message: 'Barcode doesnt exists!',
                type: 'error',
                duration: 1000
              })
              this.showCustomer = false
            }
          })
          .catch(error => {
            this.$notify({
              title: 'Error',
              message: 'Somethig went wrong!',
              type: 'error',
              duration: 1000
            })
            throw new Error(error)
          })
      } else {
        this.isFiltered = false
      }
    }, 500),
    getPromos () {
      this.points = this.filteredCustomer[0].points ? this.filteredCustomer[0].points : 0
      this.$store.dispatch('promoItems/getPromos', {points: this.points})
    },
    getPromoCustomer () {
      this.customerId = this.filteredCustomer[0].id
      this.$store.dispatch('promoCustomerAvail/getPromoCustomer', {customer_id: this.customerId})
    },
    redeemPromo (row) {
      this.promoCustomerAvailDialog = true
      this.selectedRedeem = row
    },
    onRedeem () {
      const form = {
        id: this.selectedRedeem.id,
        customer_id: this.selectedRedeem.customer_id,
        promo_id: this.selectedRedeem.promo_id,
        isAvail: 'true',
        serial_no: this.serial_number,
        date_expired: this.selectedRedeem.date_expired,
        tag: 1
      }
      if (this.serial_number !== '') {
        this.$store.dispatch('promoCustomerAvail/updatePromoCustomer', form)
          .then(() => {
            this.$notify({
              title: 'Success',
              message: 'Succesfully Redeem!',
              type: 'success'
            })
            this.getPromoCustomer()
          })
        this.promoCustomerAvailDialog = false
      } else {
        this.$notify({
          title: 'Error',
          message: 'Please input serial number',
          type: 'error'
        })
      }
    },
    clear_form: function () {
      this.barcode = ''
    },
    handleRedeem (item) {
      this.redeemInfo = Object.assign({}, item)
      this.$store.commit('pointsRedeem/setSelectedPointsRedeem', this.redeemInfo)
      this.$router.push('/points_redeem')
    },
    getRemainingDays () {
      const date1 = new Date()
      const date2 = this.filteredCustomer[0].expire_at
      const end = new Date(date2)
      const difference = end - date1
      const diff = Math.ceil(difference / (1000 * 60 * 60 * 24))
      this.remainingDays = diff + ' ' + 'Days Left'
    },
    renewExpiration () {
      this.renewDialog = true
    },
    async onSubmit () {
      // const date = this.filteredCustomer[0].expire_at
      const renew = moment(this.date).add(1, 'year').calendar()
      this.renew = renew
      const form = {
        id: this.filteredCustomer[0].id,
        barcode: this.filteredCustomer[0].barcode,
        first_name: this.filteredCustomer[0].first_name,
        last_name: this.filteredCustomer[0].last_name,
        birthdate: moment(this.filteredCustomer[0].birthdate).format('YYYY-MM-DD'),
        address: this.filteredCustomer[0].address,
        mobile_no: this.filteredCustomer[0].mobile_no ? this.filteredCustomer[0].mobile_no : 'No Number',
        customer_group_id: this.filteredCustomer[0].customer_group_id,
        vehicles: this.filteredCustomer[0].vehicles,
        expire_at: moment(this.renew).format('YYYY-MM-DD'),
        middle_initial: this.filteredCustomer[0].middle_initial,
        shift: this.shift,
        username: this.user.username,
        reference_no: this.filteredCustomer[0].reference_no
      }
      this.$store.dispatch('customer/updateCustomer', form)
        .then(() => {
          this.$notify({
            title: 'Success',
            message: 'Succesfully Renewed!',
            type: 'success'
          })
          this.renewDialog = false
        })
        .catch(error => {
          this.$notify({
            title: 'Error',
            message: 'Something went wrong!',
            type: 'error'
          })
          throw new Error(error)
        })
    },
    onCancel () {
      this.renewDialog = false
      this.promoCustomerAvailDialog = false
    },
    handleClose (done) {
      this.onCancel()
    },
    onBarcodeScanned (barcode) {
      this.barcode = barcode
    },
    resetBarcode () {
      let barcode = this.$barcodeScanner.getPreviousCode()
      console.log(barcode)
    }
  },
  watch: {
    barcode () {
      this.onInputBarcode()
    },
    page () {
      this.getTransactions()
      this.getPointsRedeem()
    }
  },
  components: {
    'app-modal': RenewModal,
    'app-redeem-modal': PromoCustomerAvailModal,
    'app-pagination': Pagination
  }
}
</script>
<style scoped>
/* .el-card__header {
  background-color: #51b156;
} */
.remain {
  font-size: 20px;
}
.points-available {
    font-size: 52px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin-left: 160px;
    position: absolute;
    margin-top: -55px;
}
.code {
    font-size: 40px;
    font-family: 'Droid Serif';
    margin-bottom: 10px;
}
.code>.el-input__inner {
    height: 50px;
    background-color: #333538;
    color: white;
}
.note {
  font-family: unset;
  font-size: 17px;
}
.barcodeheight {
  height: 100px;
}
.points {
  font-size: 65px;
}
.text {
    font-size: 14px;
  }
  .item {
    margin-bottom: 18px;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
.clearfix:after {
    clear: both
}
.button {
  padding: 5px;
  margin: 3px;
}
</style>



