<template>
    <div>
      <el-row :gutter="24">
        <el-col :span="14">
          <el-form>
            <el-form-item label="Search Customer: ">
              <el-autocomplete
                style="width: 75%"
                placeholder="Search Here...."
                hide-loading
                v-model="selectedCustomer"
                :fetch-suggestions="remoteMethod"
                @select="selectCustomerEvent">
              </el-autocomplete>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="10">
          <el-form>
            <el-form-item label="Days: ">
              <el-select @change="subtractDate" v-model="day" clearable placeholder="----- SELECT DAYS -----">
                <el-option label="15 Days" value="15"></el-option>
                <el-option label="30 Days" value="30"></el-option>
                <el-option label="60 Days" value="60"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
        <el-row>
            <el-card class="box-card" shadow="never">
              <template>
                <div slot="header" class="clearfix">
                  <span id="span_header_2"><i class="fa fa-list">&nbsp;</i><b>AGING</b></span>
                </div>
            <el-table
            :data="customers"
            border
            style="width: 100%">
            <el-table-column
            prop="id"
            label="NAME">
            <template slot-scope="scope">
              {{ scope.row.first_name}} {{ scope.row.last_name}}
            </template>
            </el-table-column>
            <el-table-column
            label="MOBILE NUMBER">
            <template slot-scope="scope">
              {{ scope.row.mobile_no ? scope.row.mobile_no : '' }}
            </template>
            </el-table-column>
            <el-table-column
            prop="customer_group_id"
            label="CUSTOMER GROUP">
            <template slot-scope="scope">
              {{ scope.row.customer_group.name}}
            </template>
            </el-table-column>
            <el-table-column
            width="250"
            label="LAST DAY OF TRANSACTION">
            <template slot-scope="scope">
              {{ lastDay(scope.row.customer_transactions) }}
            </template>
            </el-table-column>
            <el-table-column
            width="250"
            label="NO.DAYS FROM REGISTRATION">
            <template slot-scope="scope">
              {{ daysFromRegistration(scope.row.created_at) }}
            </template>
            </el-table-column>
            </el-table>
          <app-pagination :pageSize="parseInt(pagination.per_page)" :total="pagination.total" @current-change="handlePagination">
          </app-pagination>
        </template>
      </el-card>
    </el-row>
    </div>
</template>
<script>
import Pagination from '@/components/Pagination'
import { mapGetters } from 'vuex'
import _ from 'lodash'
import moment from 'moment'
export default {
  data () {
    return {
      day: '',
      page: 1,
      loading: false,
      selectedCustomer: ''
    }
  },
  computed: {
    ...mapGetters('customer', {
      customers: 'customers',
      pagination: 'pagination'
    })
  },
  created () {
    this.getCustomers()
    this.lastDay()
    this.daysFromRegistration()
  },
  watch: {
    page () {
      this.getCustomers()
    }
  },
  methods: {
    subtractDate () {
      const dateFrom = moment().subtract(this.day, 'd').format('YYYY-MM-DD')
      if (this.day !== '') {
        this.$store.dispatch('customer/getCustomers', {
          page: this.page,
          from_date: dateFrom
        })
      } else {
        this.$store.dispatch('customer/getCustomers', {
          page: this.page
        })
      }
    },
    remoteMethod: _.debounce(function (query) {
      this.loading = true
      this.$store.dispatch('customer/getCustomers', {
        s: query,
        s_field: 'first_name,last_name,barcode',
        page: this.page,
        export: 0,
        type: this.type,
        station: this.station
      }).then(() => {
        this.queryValue = query
        this.loading = false
      })
    }, 1000),
    selectCustomerEvent () {
      if (this.selectedCustomer === '') {
        this.$store.dispatch('customer/getCustomers', {
          s: '',
          s_field: 'first_name,last_name',
          page: this.page
        }).then(() => {
          this.loading = false
          this.$store.commit('appUI/setIsLoading', false)
          this.$store.commit('appUI/setIsDisable', false)
        })
      }
    },
    getCustomers () {
      const page = this.page
      this.$store.dispatch('customer/getCustomers', { page })
    },
    lastDay (row) {
      if (row.length !== 0) {
        const getLast = row.slice(-1)[0]
        const lastDay = getLast.created_at
        return moment(lastDay).format('YYYY-MM-DD')
      } else {
        return 'No Transaction'
      }
    },
    daysFromRegistration (row) {
      const startDate = new Date(row)
      const end = new Date()
      const difference = end - startDate
      const diff = Math.ceil(difference / (1000 * 60 * 60 * 24))
      if (diff > 1) {
        return diff + ' ' + 'Days' + ' ' + 'Ago'
      } else {
        return diff + ' ' + 'Day' + ' ' + 'Ago'
      }
    },
    handlePagination (page) {
      this.page = page
    }
  },
  components: {
    'app-pagination': Pagination
  }
}
</script>



